import HttpFactory from './request'

/**
 * 获取本地配置文件
 */
async function fetchConfig() {
  const { http } = HttpFactory()
  let res = await http({
    url: './config.json'
  })
  let projectAddress = window.location.host
  console.log('%c config.json配置:', 'color: red;font-size:13px', res)
  console.log('%c 本机IP:', 'color: blue;font-size:13px', projectAddress)
  return (
    {
      environments: res.environments,
      ...res[res.environments || 'PRD']
    } || {}
  )
}

export default fetchConfig


