import { CAMERA_TYPE_BOX } from '../../config'
import Camera from './index'

/**
 * 枪机 (Box Camera)
 */
class CameraBox extends Camera {
  constructor(mapViewer, ...props) {
    super(mapViewer, ...props)
    this.category = 'CAMERA' // 设备类别的名称.也用做GID的前缀
    this.categoryName = '监控'
    this.type = CAMERA_TYPE_BOX
    this.typeName = '枪机'
    this.fileName = 'qiangji'

    this.create()
  }
}
export default CameraBox
