import Intercom from './index'
import { CATEGORY_INTERCOM_HK_OUTDOOR } from '../../config'

class IntercomHkOutdoor extends Intercom {
  constructor(mapViewer, ...props) {
    super(mapViewer, ...props)
    this.category = 'INTERCOM' // 设备类别的名称.也用做GID的前缀
    this.categoryName = '对讲'
    this.type = CATEGORY_INTERCOM_HK_OUTDOOR
    this.typeName = '海康对讲室外终端'
    this.fileName = 'duijiang'

    this.create()
  }
}

export default IntercomHkOutdoor
