// import { utils } from "@ty/shared"
import * as utils from '../utils'
import MapBuildMgr from "./buildMgr"
import { curry, schedule, wait, waitSchedule } from "../scheduler"

/**
 * @module sdk-build
 * @description sdk build部分
 */

const {
  isFunction
} = utils

/**
 * @class Build
 */
class MapBuild {

  /**
   * 建筑信息初始化
   * @param {view3d} _view3d View3d对象
   */
  constructor(view3d) {
    this.view3d = view3d
    // this.mgr = new MapBuildMgr(view3d)
  }


  /**
   * 判断是否是建筑外壳 V001_JZ0001_WK
   */
  isBuildWK(gid) {
    //^V[\d]+_JZ[\d]+_WK.*$/ => 针对 V001_JZ0001_WK_001
    // /^V[\d]+_JZ[\d]+_WK$/ => 针对 V001_JZ0001_WK

    // return /^V[\d]+_JZ[\d]+_WK_?[\d]+?$/.test(gid) && this.isBuildFloor(gid) === false
    return /^V[\d]+_JZ[\d]+_WK$/.test(gid)
    // && this.isBuildFloor(gid) === false
  }

  /**
   * 判断是否是建筑标注
   */
  isBuildTitle(gid) {
    return /^MP_V[\d]+_JZ[\d]+_WK_?[\d]?$/.test(gid) && this.isBuildFloor(gid) === false
  }

  /**
   * 判断gid是否是楼层
   * @param gid
   *
   */
  isBuildFloor(gid) {
    return /^V[0]+[\d]+_JZ[0]+[\d]+_WK_[FWMB][\d]+$/.test(gid)
  }

  isBuildXP(gid) {
    return /^V[0]+[\d]+_JZ[0]+[\d]+_WK_XP[\d]*$/.test(gid)
  }

  isFloorXP(gid) {
    return /^V[0]+[\d]+_JZ[0]+[\d]+_WK_[FWMB][\d]+_XP[\d]*$/.test(gid)
  }

  /**
   * 判断gid是否是房间
   * @param gid
   *
   */
  isFloorRoom(gid) {
    return /^V[0]+[\d]+_JZ[0]+[\d]+_WK_[FWMB][\d]+_[U][\d]+_[H][\d]+$/.test(gid)
  }

  getBuildIdByWKGid(wkGid) {
    return wkGid.replace(/_WK.*/, "")
  }

  getBuildGidByBuildId(buildId) {
    return buildId.replace(/JZ([\d]+)(_?)/, "JZ$1_WK$2")
  }

  getBuildIdByTitleGid(titleGid) {
    return titleGid.replace(/_WK.*/, "").replace(/MP_/, "")
  }

  getBuildIdByFloorGid(floorGid) {
    return floorGid.replace(/_WK.*/, "")
  }

  /**
   * V001_JZ0005_WK_F001_U0001_H0001
   * @param roomGId
   */
  getBuildIdByRoomGid(roomGId) {
    return roomGId.split("_WK")[0]
  }

  /**
   * V001_JZ0005_WK_F001_U0001_H0001
   * @param roomGId
   */
  getFloorIdByRoomGid(roomGId) {
    let arr = roomGId.split("_")
    return `${arr[0]}_${arr[1]}_${arr[3]}`
  }

  /**
   * V001_JZ0001_F001
   * @param wkGid
   *
   */
  getFloorIdByFloorGid(wkGid) {
    return wkGid.replace(/_WK/, "")
  }

  /**
   * V001_JZ0001_F001
   * F001, F002
   * @param floorId
   *
   */
  getFloorNameByFloorId(floorId) {
    // return floorId.split("_")[floorId.split("_").length - 1]
    return floorId.split("_")[2]
  }

  /**
   * F1,F2
   * @param floorId
   *
   */
  getFloorNameSmallByFloorGId(floorGId) {
    let floorId = this.getFloorIdByFloorGid(floorGId)
    return this.getFloorNameSmallByFloorId(floorId)
  }

  /**
   * F1,F2
   * @param floorId
   *
   */
  getFloorNameSmallByFloorId(floorId) {
    return this.getFloorNameByFloorId(floorId).replace(/([A-Z]*)[0]*([1-9][\d]*)/, "$1$2")
  }

  getF001toF1(str) {
    return str.replace(/([\w])[0]*([\d]+)/, "$1$2")
  }

  /**
   * 建筑还原
   * @param {String} buildId 建筑Id
   * @public
   */
  resetBuild(buildId) {
    this.splitBuildingResetSync(buildId)
    this.view3d.ResetObjects()
  }

  /**
   * 楼层平移
   * @param {String} buildId 建筑Id
   */
  splitFloor(buildId, floorId, floorDistance) {
    if (this.view3d) {
      this.view3d.SplitDynamicFloor(buildId, floorId, floorDistance)
    }
  }

  /**
   * 获取当前场景里的所有建筑的数量
   * @param cb 回调函数
   * @function
   */
  getBuildingNum(cb) {
    if (isFunction(cb) === true) {
      this.view3d.GetBuildingNum(res => {
        cb && cb(res)
      })
    } else {
      console.error("cb 不是函数")
    }
  }

  /**
   * 获取当前场景里的所有建筑的名称列表
   * @param cb 回调函数
   * @function
   */
  getBuildingNames(cb) {
    if (isFunction(cb) === true) {
      this.view3d.GetBuildingNames(res => {
        cb && cb(res)
      })
    } else {
      console.error("cb 不是函数")
    }
  }

  /**
   * 异步获取当前场景里的所有建筑的名称列表
   * @param cb 回调函数
   * @function
   */
  getBuildingNamesSync() {
    return schedule(this.view3d, this.view3d.GetBuildingNames)
  }

  /**
   * 获取某个建筑是否可见？
   * @function
   * */
  getBuildingVisible(buildingName, cb) {
    if (isFunction(cb) === true) {
      this.view3d.GetBuildingVisible(buildingName, res => {
        cb && cb(res)
      })
    } else {
      console.error("cb 不是函数")
    }
  }

  /**
   * 设置建筑的可见性
   * @param opacity 透明度 0-1
   * @function
   * */
  setBuildingVisible(buildingName, buildVisible, opacity) {
    return waitSchedule(100, this.view3d, this.view3d.SetBuildingVisible, buildingName, buildVisible, opacity)
  }

  /**
   * 设置建筑的外壳是否可见
   * @param opacity 透明度 0-1
   * @function
   * */
  setBuildingWKVisible(buildingName, buildVisible, opacity) {
    this.view3d.SetBuildingWkVisible(buildingName, buildVisible, opacity)
  }

  /**
   * 恢复所有建筑的状态？
   * @function
   * */
  resetAllBuildings() {
    return waitSchedule(10, this.view3d, this.view3d.ResetAllBuildings)
  }

  /**
   * 设置所有楼层分离
   * @function
   */
  splitBuilding(buildingName, floorHeight) {
    this.view3d.SplitBuilding(buildingName, floorHeight)
  }

  /**
   * 恢复所有楼层分离的效果
   *@function*/
  splitBuildingReset(buildingName) {
    this.view3d.SplitBuildingReset(buildingName)
  }

  splitBuildingResetSync(buildingName) {
    return waitSchedule(20, this.view3d, this.view3d.SplitBuildingReset, buildingName)
  }

  /**
   * 设置所有楼层动态分离
   * @function
   * */
  splitDynamicBuilding(buildingName, floorHeight) {
    this.view3d.SplitDynamicBuilding(buildingName, floorHeight)
  }

  /**
   * 设置某个楼层动态右侧平移
   * @function
   */
  splitDynamicFloor(buildingName, floorName, floorDistance) {
    this.view3d.SplitDynamicFloor(buildingName, floorName, floorDistance)
  }

  /**
   * 恢复平移的楼层
   * @function
   * */
  splitFloorReset(buildingName, floorName) {
    return this.view3d.SplitFloorReset(buildingName, floorName)
  }

  /**
   * 获取某个建筑的所有楼层数量
   * @function
   */
  getFloorNum(buildingName, cb) {
    if (isFunction(cb) === true) {
      this.view3d.GetFloorNum(res => {
        cb && cb(res)
      })
    } else {
      console.error("cb 不是函数")
    }
  }

  /**
   * 获取某个建筑的所有楼层的名称列表
   * @function
   */
  getFloorNames(buildingName, cb) {
    console.log("call getFloorNames: ", buildingName)
    console.log(isFunction(cb))
    if (isFunction(cb)) {
      this.view3d.GetFloorNames(buildingName, cb)
    } else {
      console.error("cb 不是函数")
    }
  }

  /**
   * 同步获取某个建筑的所有楼层的名称列表
   * @function
   */
  getFloorNamesSync(buildingName) {
    return schedule(this.view3d, this.view3d.GetFloorNames, buildingName)
  }

  /**
   * 获取某个建筑某个楼层是否可见
   * @function
   */
  getFloorVisible(buildingName, floorName, cb) {
    if (isFunction(cb) === true) {
      this.view3d.GetFloorVisible(buildingName, floorName, res => {
        cb && cb(res)
      })
    } else {
      console.error("cb 不是函数")
    }
  }

  /**
   * 设置某个建筑某个楼层的可见性
   * @function
   * @param buildingName 例 V001_JZ0001
   * @param floorName F004
   * @param floorVisible true/false
   * @param opacity
   *
   */
  setFloorVisible(buildingName, floorName, floorVisible, opacity) {
    return waitSchedule(10, this.view3d, this.view3d.SetFloorVisible, buildingName, floorName, floorVisible, opacity)
  }

  /**
   * 获取某个建筑某个楼层之上的楼层数量
   * @function
   */
  getUpFloorNum(buildingName, floorName, cb) {
    if (isFunction(cb) === true) {
      this.view3d.GetUpFloorNum(buildingName, floorName, res => {
        cb && cb(res)
      })
    } else {
      console.error("cb 不是函数")
    }
  }

  /**
   * 获取某个建筑某个楼层之上的楼层名称列表
   * @function
   */
  getUpFloorNames(buildingName, floorName, cb) {
    this.view3d.GetUpFloorNames(buildingName, floorName, res => {
      cb && cb(res)
    })
  }

  /**
   * 获取某个建筑某个楼层之下的楼层数量
   * @function
   */
  getDownFloorNum(buildingName, floorName, cb) {
    this.view3d.GetDownFloorNum(buildingName, floorName, res => {
      cb && cb(res)
    })
  }

  /**
   * 获取某个建筑某个楼层之下的楼层名称列表
   * @function
   */
  GetDownFloorNames(buildingName, floorName, cb) {
    this.view3d.GetDownFloorNum(buildingName, floorName, res => {
      cb && cb(res)
    })
  }

  /**
   * 获取某个建筑某个楼层内的房间数量
   * @function
   */
  getFloorRoomNum(buildingName, floorName, cb) {
    this.view3d.GetFloorRoomNum(buildingName, floorName, res => {
      cb && cb(res)
    })
  }

  /**
   * 获取某个建筑某个楼层内的房间名称列表
   * @function
   *
   */
  getFloorRoomNames(buildingName, floorName, cb) {
    return schedule(this.view3d, this.view3d.GetFloorRoomNames, buildingName, floorName)
  }

  /**
   * 获取某个建筑某个楼层内某个房间是否可见
   * @function
   */
  getFloorRoomVisible(buildingName, floorName, roomName, cb) {
    this.view3d.GetFloorRoomVisible(buildingName, floorName, roomName, res => {
      cb && cb(res)
    })
  }

  /**
   * 设置某个建筑某个楼层内某个房间是否可见
   * @function
   */
  setFloorRoomVisible(buildingName, floorName, roomName, floorVisible) {
    this.view3d.SetFloorRoomVisible(buildingName, floorName, roomName, floorVisible)
  }

  /**
   * @description 设置地面的显示和隐藏
   * @function
   */
  async setGroundVisible(visible) {
    // this.view3d.SetGroundVisible(visible)
    return waitSchedule(10, this.view3d, this.view3d.SetGroundVisible, visible)
  }
}


export default MapBuild


