let treeUtil = {
  treeDfs(tree, func) {
    tree.forEach((node) => {
      func(node)
      node.children && treeUtil.treeDfs(node.children, func)
    })
  },
  /**
   * @description 树转列表
   */
  treeToList: (arr) => {
    let res = [] // 用于存储递归结果（扁平数据）
    // 递归函数
    let fn = (source) => {
      source.forEach((el) => {
        res.push(el)
        el.children && el.children.length > 0 ? fn(el.children) : '' // 子级递归
      })
    }
    fn(arr)
    return res
  },

  /**
   * @description 列表转树
   */
  list2Tree: (arr) => {
    // 双循环
    arr.forEach((e) => {
      // 父循环
      arr.forEach((y) => {
        // 子循环
        if (y.pid === e.id) {
          // 子取parentId和父id做对比
          if (!e.children) {
            e.children = []
          }
          e.children.push(y) // 把子添加父
        }
      })
    })
    arr = arr.filter((ele) => ele.pid === null) // 过滤
    return arr
  }
}

export default treeUtil
