// import { utils } from '@ty/shared'
import * as utils from '../utils'
import { functionExist, logError } from '../error'
import { doSchedule, curry, schedule, waitSchedule } from '../scheduler'

const { isFunction, EMPTY_OBJ, debounce } = utils

class MapCore {
  constructor() {
    this._view3d = null
    this.config = EMPTY_OBJ
  }

  /**
   * 提供 view3d 的 getter
   * @returns {null}
   */
  get view3d() {
    return this._view3d
  }

  /**
   * view3d setter 外部基本不应该使用，一般只在内部调用
   * @param view3d
   */
  set view3d(view3d) {
    this._view3d = view3d
  }

  async createMap(config) {
    this.config = config
    let view3d = {}
    const self = this

    // 加载CSS文件
    // handleInsertCssFiles()

    if (!window.MapVision || !window.MapVision.View3d) {
      // todo: log file not include error.
      if (config.error && isFunction(config.error)) {
        config.error({
          code: 1,
          msg: `mapvision3d.min.js file was not include.`
        })
      }
      return
    }

    return doSchedule({
      task: () => {
        let conf = {
          id: config.id,
          url: config.url,
          projectId: config.projectId,
          mapkey: config.mapkey,
          token: config.token
        }
        view3d = new MapVision.View3d(conf)
        return curry(view3d, view3d.Open, (res) => {
          //连接成功
          if (Number(res.code) === 200) {
            self._view3d = view3d
            self.view3d = view3d
          } else {
            logError({
              msg: 'connection fail! check if MapVisionPServer is running!'
            })
            config.error(res)
            // TODO: 地图链接失败时，弹窗提示信息
            return
          }

          if (config.autoResize) {
            // 给 window 窗口绑定 resize 事件, resize 后调整地图的分辨率,避免模型被拉伸
            handleAddWindowResizeResolution(self, config.autoResizeTicker)
          }

          // 清空地图
          this._view3d.OverLayerRemoveAll()

          // 建筑状态初始化
          if(config.resetBuildings) {
            this._view3d.ResetAllBuildings()
          }

          // 隐藏地图指北
          this.setNorthControl(config.northControl)


          if (config.defaultSetResolution) {
            // 设置分辨率
            this.setResolution(config.id)
          }

          // 重新设置参数
          this.setParameters()

          // 设置鼠标操作
          this.setMouseEnable(config.enableMouse)

          // 设置键盘操作
          this.setKeyBoardEnable(config.enableKeyboard)

          //停止动画
          this._view3d.Stop()
          this._view3d.Clear()

          // 内置的鼠标是否可见
          this.setMouseCursorVisible(config.internalMouseVisible)

          // 插入遮罩元素
          this._insertMask()

          if (config.defaultLocate) {
            // 默认定位
            view3d.ResetHome()
          }
        })
      }
    })
  }

  /**
   * 销毁地图实例 并执行相关卸载的逻辑
   */
  destroy() {
    removeWindowResizeResolution()
    this.view3d = null
  }

  /**
   *
   * @private
   */
  _insertMask() {
    const mapContainerId = this.config.id
    const $mapContainer = document.getElementById(mapContainerId)
    if ($mapContainer) {
      const $mapContainerParent = $mapContainer.parentNode
      if ($mapContainerParent) {
        const $mask = document.createElement('div')
        $mask.id = mapContainerId + '_mask'
        $mask.style.position = 'absolute'
        $mask.style.left = '0'
        $mask.style.top = '0'
        $mask.style.width = '100%'
        $mask.style.height = '100%'
        $mask.style.zIndex = '1'
        $mask.style.display = 'none'
        $mask.style.backgroundColor = 'transparent'
        $mask.style.opacity = '0'
        $mapContainerParent.appendChild($mask)
      }
    }
  }

  _removeMask() {
    const mapContainerId = this.config.id
    const $mapContainer = document.getElementById(mapContainerId)
    if ($mapContainer) {
      const $mapContainerParent = $mapContainer.parentNode

      if ($mapContainerParent) {
        $mapContainerParent.removeChild($mapContainer)
      }
    }
  }

  /**
   * @desc
   * @private 显示地图遮罩 div
   */
  showCover() {
    const mapContainerId = this.config.id
    const $mask = document.getElementById(mapContainerId + '_mask')
    $mask && ($mask.style.display = 'block')
  }

  /**
   * @desc 隐藏地图遮罩 div
   * @private
   */
  hideCover() {
    const mapContainerId = this.config.id
    const $mask = document.getElementById(mapContainerId + '_mask')
    $mask && ($mask.style.display = 'none')
  }

  /**
   * 设置地图分辨率
   * @param domId {String} 地图容器id
   */
  setResolution(domId = 'mapVision3d') {
    const config = this.config
    const view3d = this._view3d
    let $mapContainer = document.getElementById(domId)
    if ($mapContainer && functionExist(view3d, 'SetResolution')) {
      let dpi = window.devicePixelRatio * (utils.osUtil.isPc ? 1 : 3)
      view3d.SetResolution(
        $mapContainer.clientWidth * dpi * config.resolutionScale,
        $mapContainer.clientHeight * dpi * config.resolutionScale
      )
    }
  }

  /**
   *
   * @param mapName {String} 地图名称，必须在生产地图的时候给出，目前程序控制不了
   */
  openMap(mapName = 'default') {
    if (functionExist(this._view3d, 'OpenMap')) {
      this._view3d.OpenMap(mapName)
    }
  }

  /**
   * 设置鼠标的启用/禁用状态
   * @param mouseEnable {Boolean} 鼠标是否启用
   */
  setMouseEnable(mouseEnable = true) {
    this._view3d.enableMouse = mouseEnable
  }

  /**
   * 设置键盘启用/禁用状态
   * @param keyboardEnable {Boolean} 键盘是否启用
   */
  setKeyBoardEnable(keyboardEnable = true) {
    this._view3d.enableKeyboard = keyboardEnable
  }

  /**
   * 隐藏植被
   */
  setTreeVisible(visible = true) {
    const view3d = this._view3d
    if (functionExist(view3d, 'UpdateObjectVisible')) {
      return waitSchedule(
        10,
        view3d,
        view3d.UpdateObjectVisible,
        'InstancedFoliageActor',
        visible
      )
    }
  }

  /**
   * 隐藏光
   */
  setDirectionalLightVisible(visible = true) {
    const view3d = this._view3d
    if (functionExist(view3d, 'UpdateObjectVisible')) {
      return waitSchedule(
        10,
        view3d,
        view3d.UpdateObjectVisible,
        'DirectionalLight',
        visible
      )
    }
  }

  /**
   * 隐藏天空
   */
  setDynamicSkyVisible(visible) {
    const view3d = this._view3d
    if (functionExist(view3d, 'UpdateObjectVisible')) {
      return waitSchedule(
        10,
        view3d,
        view3d.UpdateObjectVisible,
        'Ultra_Dynamic_Sky',
        visible
      )
    }
  }

  /**
   * 隐藏天空球
   */
  setSkySphereVisible(visible) {
    const view3d = this._view3d
    if (functionExist(view3d, 'UpdateObjectVisible')) {
      return waitSchedule(
        10,
        view3d,
        view3d.UpdateObjectVisible,
        'BP_Sky_Sphere',
        visible
      )
    }
  }

  /**
   * 获取键盘启用/禁用状态
   * @param keyboardEnable {Boolean} 键盘是否启用
   */
  getKeyBoardEnable() {
    return this._view3d.enableKeyboard
  }

  /**
   * 设置地图内置的鼠标是否可见
   * @param cursorVisible
   */
  setMouseCursorVisible(cursorVisible = false) {
    if (functionExist(this._view3d, 'SetParameters')) {
      this.setParameters({
        showmouse: cursorVisible
      })
    }
  }

  /**
   * 获取场景中的太阳时间
   * @return {Promise}
   */
  async getSceneSunInfo() {
    const view3d = this._view3d
    if (functionExist(view3d, 'GetScenceSunInfo')) {
      return schedule(view3d, view3d.GetScenceSunInfo)
    }
  }

  /**
   * 设置场景中的太阳时间
   * @param info
   * @returns {Promise<*>}
   */
  setSceneSunInfo(info) {
    // info 的格式如下：
    /*
     {
     year: 2020,
     month: 7,
     day: 7,
     hour: 18,
     minute: 10
     }

     */
    const view3d = this._view3d
    if (functionExist(view3d, 'SetScenceSunInfo')) {
      return waitSchedule(10, view3d, view3d.SetScenceSunInfo, info)
    }
  }

  /**
   * 设置指北针控件的可见性和位置
   * @param visible {Boolean}
   * @param x {Number}
   * @param y {Number}
   * @param scale {Number}
   */
  setNorthControl(visible = false, x = 0, y = 0, scale = 1.0) {
    this._view3d.SetNorthControl(visible, x, y, scale)
  }

  /**
   * 关闭地图
   */
  close() {
    const view3d = this._view3d
    if (functionExist(view3d, 'Close')) {
      view3d.Close()
      this.destroy()
    }
  }

  /**
   * 给地图设置参数
   * @param parameter
   * 所有参数：
   * {
   *  prefix: 'MP,TEMP,JZ,V',// MP,TEMP,JZ,V
   * 	speedroute: 10, // 路径巡游的速率,每渲染帧的步长，单位，厘米/帧
   * 	keyboardrate: 1.0, // 键盘移动速率 范围：0.001——1.0
   * 	showmouse: true, // 是否显示地图内部的鼠标
   * 	mouse_selected: true, // 鼠标点选效果高亮，是否起作用
   * 	max_distance: 1000000, // 1万米，最远缩放距离
   * 	unit: 'cm' // 场景返回数据单位， 默认厘米   输入m返回为米   其他值为厘米
   * }
   */
  setParameters(parameter = null) {
    const view3d = this._view3d
    view3d.SetParameters(parameter)
  }

  /**
   * 设置点选物体高亮 - 默认描边的效果
   */
  enableHighLight() {
    const view3d = this._view3d
    view3d.SetParameters({
      mouse_selected: true
    })
  }

  /**
   * 取消点选物体高亮效果
   */
  disableHighLight() {
    const view3d = this._view3d
    view3d.SetParameters({
      mouse_selected: false
    })
  }

  /**
   * 地图状态重置
   */
  clear() {
    const view3d = this._view3d
    if (functionExist(view3d, 'Clear')) {
      view3d.Clear()
    }
  }

  /**
   * 停止动画等
   */
  stop() {
    const view3d = this._view3d
    if (functionExist(view3d, 'Stop')) {
      view3d.Stop()
    }
  }

  /**
   * 返回 MapVisionPServer服务 是否关闭
   *
   */
  isClose() {
    const view3d = this._view3d
    if (functionExist(view3d, 'IsClose')) {
      return view3d.IsClose()
    }
  }

  /**
   * 设置地图状态
   */
  setMapStatus(code = null) {
    /*
     MapVision.MapStatusType =>
     Pan: 0 // 漫游状态
     Select: 1 // 选择对象状态
     SelectObjectCallback: 3  // 选择对象状态 返回对象
     SelectPositionCallback: 2  // 选择对象状态 返回对象
     */
    const view3d = this._view3d
    if (functionExist(view3d, 'SetMapStatus')) {
      view3d.SetMapStatus(code)
    }
  }
}

let debouncedResizeHandler

function handleAddWindowResizeResolution(mapCoreIns, ticker = 300) {
  //  绑定窗口大小事件
  debouncedResizeHandler = debounce(() => {
    mapCoreIns.setResolution(mapCoreIns.config.id)
  }, ticker)
  window.addEventListener('resize', debouncedResizeHandler)
}

function removeWindowResizeResolution() {
  if (debouncedResizeHandler) {
    window.removeEventListener('resize', debouncedResizeHandler)
  }
}

export default MapCore
