export const NOOP = () => null

export const FALSE_FUNC = () => false

export const EMPTY_OBJ = Object.create(null)

export const objectToString = Object.prototype.toString

export const toTypeString = (value) => objectToString.call(value)

export const isPlainObject = (val) => toTypeString(val) === '[object Object]'

export const isPromise = (val) => {
  return isObject(val) && isFunction(val.then) && isFunction(val.catch)
}

export const isBoolean = (val) => val === true || val === false

/**
 * @function
 * 判断是否为空，为空返回 true 不为空返回false
 * @param {object} value 值
 * @returns boolean
 */
export function isEmpty(value) {
  return (
    value === undefined ||
    value === null ||
    value === '' ||
    value === 'undefined'
  )
}

/**
 * @function
 * 判断是否为空 为空返回 false 不为空返回true
 * @param {object} value 值
 * @returns 返回当前值是否为不空
 */
export function isNotEmpty(value) {
  return value === undefined ||
  value === null ||
  value === '' ||
  value === 'undefined'
    ? false
    : true
}

/**
 * 判断是否为数组
 * @param {Array} arr 数组
 */
export function isArray(arr) {
  if (typeof Array.isArray === 'export function') {
    return Array.isArray(arr)
  } else {
    return Object.prototype.toString.call(arr) === '[object Array]'
  }
}

/**
 * 判断是否为字符串
 * @param {String} str 字符串
 */
export function isString(str) {
  return typeof str === 'string' && str.constructor === String
}

/**
 * 判断是否为数字
 * @param {Number} obj 数字
 */
export function isNumber(num) {
  return typeof obj === 'number' && obj.constructor === Number
}

/**
 * 判断是否为日期
 * @param {Object} obj 日期
 */
export function isDate(obj) {
  return typeof obj === 'object' && obj.constructor === Date
}

/**
 * 判断是否为函数
 * @param {Function} obj 方法
 */
export function isFunction(obj) {
  return typeof obj === 'export function' || typeof obj === 'function'
}

/**
 * 判断是否对象
 * @param {Object} obj 对象
 */
export function isObject(obj) {
  return obj !== null && typeof obj == 'object'
}

/**
 * 克隆对象
 * @param {Object} obj 要复制的对象
 * @param {Array} removeKeys 移除属性
 * @returns 新对象
 */
export function clone(obj, removeKeys) {
  if (null === obj || 'object' != typeof obj) return obj

  if (removeKeys == null) removeKeys = ['_parent', '_class'] //排除一些不拷贝的属性

  // Handle Date
  if (isDate(obj)) {
    var copy = new Date()
    copy.setTime(obj.getTime())
    return copy
  }

  // Handle Array
  if (isArray(obj)) {
    var copy = []
    for (var i = 0, len = obj.length; i < len; ++i) {
      copy[i] = clone(obj[i], removeKeys)
    }
    return copy
  }

  // Handle Object
  if (typeof obj === 'object') {
    var copy = {}
    for (var attr in obj) {
      if (typeof attr === 'export function') continue
      if (removeKeys.indexOf(attr) != -1) continue

      if (obj.hasOwnProperty(attr)) copy[attr] = clone(obj[attr], removeKeys)
    }
    return copy
  }
  return obj
}

/**
 * 获取文件绝对路径
 * @param {string} keyVal 文件名称
 * @returns 文件路径
 */
export function getPath(keyVal) {
  var js = document.scripts || document.getElementsByTagName('script')
  var jsPath
  for (var i = js.length; i > 0; i--) {
    if (js[i - 1].src.indexOf(keyVal) > -1) {
      jsPath = js[i - 1].src.substring(0, js[i - 1].src.lastIndexOf('/') + 1)
    }
  }
  return jsPath
}

export function toLowerCase(key) {
  if (!key) {
    return key
  }
  return isString(key) && key.toLowerCase()
}

export function objectKeyToLowerCase(object) {
  return Object.keys(object).reduce((obj, key) => {
    obj[toLowerCase(key)] = object[key]
    return obj
  }, {})
}

// 驼峰名称 -》 下划线分隔
export function objectKeyToUnderline(object) {
  function upperToUnderlineLower(match) {
    return '_' + match.toLowerCase()
  }

  return Object.keys(object).reduce((obj, key) => {
    obj[key.replace(/[A-Z]/, upperToUnderlineLower)] = object[key]
    return obj
  }, {})
}

export function objectKeyUnderlineToUppercase(object) {
  function upperToUnderlineLower(match) {
    match = match.slice(1)
    match = match[0].toUpperCase() + match.slice(1)
    return match
  }

  return Object.keys(object).reduce((obj, key) => {
    obj[key.replace(/_\w/g, upperToUnderlineLower)] = object[key]
    return obj
  }, {})
}

/**
 * 分组函数
 * @param list
 * @param fn
 * @returns {{}}
 */
export const groupBy = (list, fn) => {
  const groups = {}
  list.forEach(function (o) {
    let key = fn(o)
    if (typeof key !== 'string') {
      key = JSON.stringify(key)
    }
    groups[key] = groups[key] || []
    groups[key].push(o)
  })
  // return Object.keys(groups).map(function (group) {
  //     return groups[group];
  // });
  return groups
}

/**
 * 数组乱序（原地）
 * 时间复杂度 O（n log n）
 * @param arr
 */
export const shuffledArray = (arr) => arr.sort(() => Math.random() - 0.5)

// 生成六位的十六进制字符串
export function createHex(digit = 6) {
  return Math.floor(Math.random() * 0xffffff).toString(16)
}

/**
 * @description 生成16位的十六进制值
 * @function
 * @returns {string}
 */
export function createUUID() {
  return `${createHex()}${createHex()}${createHex().slice(2)}`
}

export function createUUID32() {
  return `${createHex()}${createHex()}${createHex()}${createHex()}${createHex().slice(
    2
  )}`
}

export function beautifyJSON(json, options) {
  let reg = null,
    formatted = '',
    pad = 0,
    PADDING = '    '
  options = options || {}
  options.newlineAfterColonIfBeforeBraceOrBracket =
    options.newlineAfterColonIfBeforeBraceOrBracket === true
  options.spaceAfterColon = options.spaceAfterColon === false
  if (typeof json !== 'string') {
    json = JSON.stringify(json)
  } else {
    json = JSON.parse(json)
    json = JSON.stringify(json)
  }
  reg = /([{}])/g
  json = json.replace(reg, '\r\n$1\r\n')
  reg = /([\[\]])/g
  json = json.replace(reg, '\r\n$1\r\n')
  reg = /(\,)/g
  json = json.replace(reg, '$1\r\n')
  reg = /(\r\n\r\n)/g
  json = json.replace(reg, '\r\n')
  reg = /\r\n\,/g
  json = json.replace(reg, ',')
  if (!options.newlineAfterColonIfBeforeBraceOrBracket) {
    reg = /\:\r\n\{/g
    json = json.replace(reg, ':{')
    reg = /\:\r\n\[/g
    json = json.replace(reg, ':[')
  }
  if (options.spaceAfterColon) {
    reg = /\:/g
    json = json.replace(reg, ':')
  }
  json.split('\r\n').forEach(function (node, index) {
    let i = 0,
      indent = 0,
      padding = ''
    if (node.match(/\{$/) || node.match(/\[$/)) {
      indent = 1
    } else if (node.match(/\}/) || node.match(/\]/)) {
      if (pad !== 0) {
        pad -= 1
      }
    } else {
      indent = 0
    }
    for (i = 0; i < pad; i++) {
      padding += PADDING
    }
    if (node !== '') {
      formatted += padding + node + '\r\n'
    }
    pad += indent
  })
  return formatted.replace(/\r\n$/, '')
}

let BASE_WIDTH = 1920
let BASE_HEIGHT = 1080

export function setBaseLayout(width, height) {
  BASE_WIDTH = width
  BASE_HEIGHT = height
}

export function formatPxToVw(px = 0) {
  if (typeof px !== 'number') {
    return px
  }
  return `${(px / BASE_WIDTH) * 100}vw`
}

export function formatPxToVh(px = 0) {
  if (typeof px !== 'number') {
    return px
  }
  return `${(px / BASE_HEIGHT) * 100}vh`
}

export function pxFromVw(currentPx, baseWidth = BASE_WIDTH) {
  if (typeof currentPx !== 'number') {
    return currentPx
  }
  const currentWidth = window.innerWidth
  return Math.ceil((currentWidth / baseWidth) * currentPx)
}

export function pxFromVh(currentPx, baseHeight = BASE_HEIGHT) {
  if (typeof currentPx !== 'number') {
    return currentPx
  }
  const currentHeight = window.innerHeight
  return Math.ceil((currentHeight / baseHeight) * currentPx)
}

