import { CATEGORY_DOOR_HK_FACE_ACCESS_TERMINAL } from '../../config'
import Door from './index'

// 门禁 - 海康人脸识别一体机
class DoorHKFaceAccessTerminal extends Door {
  constructor(mapViewer, ...props) {
    super(mapViewer, ...props)
    this.category = 'DOOR' // 设备类别的名称.也用做GID的前缀
    this.categoryName = '门禁'
    this.type = CATEGORY_DOOR_HK_FACE_ACCESS_TERMINAL
    this.typeName = '海康人脸识别一体机'
    this.fileName = 'menjin'

    this.create()
  }
}

export default DoorHKFaceAccessTerminal
