// import { utils } from '@ty/shared'
import * as utils from '../utils'
import { functionExist, logError, warn } from '../error'

/**
 * @module sdk-event
 * @description sdk事件
 */

const { isFunction, NOOP, isBoolean, FALSE_FUNC, debounce, throttle } = utils

const MAP_STATUS_NORMAL = 'MAP_STATUS_NORMAL' // 普通状态
const MAP_STATUS_DRAG = 'MAP_STATUS_DRAG' // 拖动的状态

const MOUSE_STATUS_DOWN = 'MOUSE_STATUS_DOWN'
const MOUSE_STATUS_MOVE = 'MOUSE_STATUS_MOVE'
const MOUSE_STATUS_DRAG = 'MOUSE_STATUS_DRAG'
const MOUSE_STATUS_UP = 'MOUSE_STATUS_UP'

// 注册到 video 父元素上的基础鼠标事件
const MOUSE_MOVE_KEY = 'mousemove' // 鼠标移动
const MOUSE_DOWN_KEY = 'mousedown' // 鼠标按下
const MOUSE_UP_KEY = 'mouseup' // 鼠标抬起
const MOUSE_WHEEL = 'mousewheel' // 滚轮缩放
const MOUSE_DRAG = 'mousedrag' // 拖拽的Key， 仅用来做事件缓存是的可以，实际绑定到 DOM 的还是 mousemove 事件

let mapStatus = MAP_STATUS_NORMAL
let mouseStatus = MOUSE_STATUS_MOVE // 正常的鼠标状态是移动
let mapMoveTimer = null

const mouseEventGroup = [
  MOUSE_MOVE_KEY,
  MOUSE_DOWN_KEY,
  MOUSE_UP_KEY,
  MOUSE_WHEEL,
  MOUSE_DRAG
]

const defaultConfig = {
  prefix: '', // 当 isDouble 为true是，原始的prefix,会保存到这里
  isDouble: false, // 内部判断是否双击事件，
  compare: FALSE_FUNC,
  isOnce: false,
  internal: false,
  key: +new Date() // 回调指定的key，删除时可用
}

/**
 * @class MapEvent
 */
class MapEvent {
  constructor(view3d, mapViewer) {
    this._view3d = view3d
    this.mapViewer = mapViewer
    // 通过显示的保存指定的前缀
    this.prefix = new Set()

    this.debug = false // 是否显示点击的对象属性

    /*
     prefixAndCallbackMap 是一个三层结构：
     {
     'prefix': {
      prefix: 'prefix',
      callback: {
         // 单击的回调
         click: [{
         config: config,
         callback: callback
        }],

         // 双击的回调
         double: [{
           config: config,
           callback: callback
         }]
      }
     }
     */
    this.prefixAndCallbackMap = new Map()

    this._lastClickTimestamp = +new Date()

    // 是否注册过鼠标滚轮事件
    this._hasRegisterMouseWheelHandler = false

    // 在点击事件触发时，如果都没有匹配到注册的事件回调，要执行上一次回调的清理函数
    this.cleanupCallback = null

    // 同一个prefix，同时注册单击和双击事件，在300ms回调后来判断是否执行单击
    this._clickCallbackTimer = new Map()

    // 响应点击和是否拖动的时间间隔，在这个间隔内，如果触发了地图的拖动，将不响应地图的点击事件。
    this._dragTicker = 100

    // 单击和双击的间隔值 ms, 如果同一个 prefix，既有单击，又有双击事件，第一次单击会被延迟
    // 同时等待 _doubleClickTicker 的时间，如果再次触发单击，则触发双击的回调，如果没有再次触发单击，则触发单击回调
    this._doubleClickTicker = 180

    // 因为每次缩放，都去请求一次当前相机的位置，所以，如果上一次请求的接口没有返回，就不去获取
    this.getCurrentPositionComplete = false

    // 保存DOM事件key的状态，来避免重复绑定事件
    this.mouseEventStatusMap = new Map(
      mouseEventGroup.map(key => [key, false])
    )

    // 默认注册内部的鼠标拖拽事件，来避免拖拽是误触点击事件
    this._internalHandlerMouseMoveEvent()
  }

  // 内部监听鼠标按下，抬起的事件
  _internalHandlerMouseMoveEvent() {
    // 这里的回调不需要执行逻辑，只需要让内部的处理函数做好鼠标和地图状态的管理即可
    const internalMouseUpHandler = () => {
    }

    const internalMouseDownHandler = () => {
    }

    const internalMouseDragHandler = () => {
    }

    this.onMouseUp(internalMouseUpHandler, {
      throttle: false
    })

    // 鼠标按下的事件不能节流 ！！！
    // 否则会影响拖动事件的判断
    this.onMouseDown(internalMouseDownHandler, {
      throttle: false
    })

    // 拖拽的事件，可以节流，避免回调触发过于频繁
    this.onDrag(internalMouseDragHandler, {
      ticker: 50,
      throttle: true
    })
  }

  // 执行所有的 DOM 回调
  _flushMouseEvent(key, ...args) {
    const mouseWheelMap = this.prefixAndCallbackMap.get(key)
    if (mouseWheelMap) {
      const mouseWheelEventCallbackMap = mouseWheelMap.get('callback')
      const mouseWheelEventCallbackSet =
        mouseWheelEventCallbackMap.get('click')
      mouseWheelEventCallbackSet.forEach((oneMouseWheelHandlerMap) => {
        if (oneMouseWheelHandlerMap.get('callback')) {
          oneMouseWheelHandlerMap.get('callback')(...args)
        }
      })
    }
  }

  // 注册鼠标事件到地图容器上
  _registerMouseEvent(key, callback, option) {
    // 不需要重复注册事件到 DOM 元素上
    if (this.mouseEventStatusMap.get(key)) {
      return
    }

    this.mouseEventStatusMap.set(key, true)

    const mapViewContainerId = this.mapViewer.core.config.id
    const $mapViewContainer = document.getElementById(mapViewContainerId)

    if ($mapViewContainer) {
      $mapViewContainer.addEventListener(option.domEvent, callback, false)
    }
  }

  // 移除，注册在地图容器元素之上的 DOM 事件
  _removeMouseEvent(key, callback) {
    const existCallbackMapKey = mouseEventGroup.filter(
      (mouseKey) => mouseKey === key
    )[0]

    if (existCallbackMapKey) {
      // 再去删除缓存的函数
      this._removeCacheCallback(key, callback)
    }
  }

  // 缓存所有注册的鼠标事件（DOM 事件）
  _cacheMouseEvent(
    key = '',
    callback = () => null,
    handler = () => null,
    config = {}
  ) {
    // 默认的配置
    const defaultOption = {
      // 节流和防抖只能二选一
      throttle: true,
      debounce: false,
      ticker: 100,

      // 实际绑定到 DOM 事件类型
      domEvent: key
    }

    const option = Object.assign({}, defaultOption, config)

    if (isFunction(callback)) {
      let mouseHandler = handler

      // 通过配置，选择防抖 、节流
      if (option.throttle) {
        mouseHandler = throttle(
          mouseHandler,
          option.ticker
        )
      }

      if (option.debounce) {
        mouseHandler = debounce(
          mouseHandler,
          option.ticker,
          true
        )
      }

      this._cacheCallback(key, callback)

      this._registerMouseEvent(key, mouseHandler, option)
    }
  }

  /**
   * 实际鼠标缩放的处理函数
   * @private
   */
  _mouseWheelHandler(e) {
    // 避免获取相机位置的接口没有返回，而重复获取，导致出现视角回调获取位置的顺序出错
    if (this.getCurrentPositionComplete) {
      return
    }

    if (
      this.mapViewer.camera &&
      this.mapViewer.camera.getCurrentPosition
    ) {

      this.getCurrentPositionComplete = true

      // 缩放的同时，获取一次当前的相机位置，主要用来判断当前的高度
      this.mapViewer.camera.getCurrentPosition().then((position) => {
        this.getCurrentPositionComplete = false
        this._flushMouseEvent(MOUSE_WHEEL, e, position)
      })
    }
  }

  /**
   * 实际鼠标移动的处理函数
   * @param e
   * @private
   */
  _mouseMoveHandle(e) {
    let eventKey = MOUSE_MOVE_KEY

    // 如果鼠标移动过程中，处于鼠标按下的状态，则是在拖动地图
    if (mouseStatus === MOUSE_STATUS_DOWN || mouseStatus === MOUSE_STATUS_DRAG) {
      mouseStatus = MOUSE_STATUS_DRAG
      mapStatus = MAP_STATUS_DRAG
      eventKey = MOUSE_DRAG

      // 否则是正常的鼠标移动
    } else {
      mouseStatus = MOUSE_STATUS_MOVE
      mapStatus = MAP_STATUS_NORMAL
    }

    if (mapMoveTimer) {
      clearTimeout(mapMoveTimer)
    }

    // 如果移动过程中，鼠标移到弹窗上，可能没有触发mouseup事件，这里给个定时器，100毫秒以后，移除拖动状态
    // mapMoveTimer = setTimeout(() => {
    //   // 如果是拖动过程中，没有触发鼠标抬起
    //   if (mouseStatus === MOUSE_STATUS_DRAG) {
    //     // this._mouseUpHandler(e)
    //   }
    // }, 100)

    // 同时这里要跟拖拽区分开，因为拖拽也会出发移动，这里要判断,只处理移动事件
    this._flushMouseEvent(eventKey, e)
  }

  _mouseDownHandler(e) {
    mapStatus = MAP_STATUS_NORMAL
    mouseStatus = MOUSE_STATUS_DOWN
    this._flushMouseEvent(MOUSE_DOWN_KEY, e)
  }

  _mouseUpHandler(e) {
    mapStatus = MAP_STATUS_NORMAL

    mouseStatus = MOUSE_STATUS_UP

    this._flushMouseEvent(MOUSE_UP_KEY, e)
  }

  getCallbacks(prefix) {
    const callbackMap = this.prefixAndCallbackMap.get(prefix)
    if (callbackMap) {
      return Array.from(callbackMap.get('callback').click).map(
        (oneCallbackMap) => Object.fromEntries(oneCallbackMap)
      )
    }
  }

  /**
   * @desc 设置响应点击和确认是否拖动的时间间隔， 默认 100ms.
   */
  setDragTicker(time = 100) {
    this._dragTicker = time
  }

  /**
   * 设置单击和双击的间隔时间，单位： ms.
   * @param time
   */
  setDoubleClickTicker(time = 150) {
    this._doubleClickTicker = time
  }

  // 注册鼠标缩放事件
  onZoom(callback, config = {}) {
    this._cacheMouseEvent(MOUSE_WHEEL, callback, this._mouseWheelHandler.bind(this), config)
  }

  /**
   * 移除鼠标缩放事件
   */
  offZoom(callback) {
    if (isFunction(callback)) {
      this.remove(MOUSE_WHEEL, callback)
    }
  }

  // 注册鼠标移动事件可能导致地图反应慢.先不处理
  onMouseMove(callback, config = {}) {
    this._cacheMouseEvent(MOUSE_MOVE_KEY, callback, this._mouseMoveHandle.bind(this), config)
  }

  // 移除鼠标移动事件
  offMouseMove(callback) {
    this.remove(MOUSE_MOVE_KEY, callback)
  }

  // 注册鼠标按下
  onMouseDown(callback, config) {
    this._cacheMouseEvent(MOUSE_DOWN_KEY, callback, this._mouseDownHandler.bind(this), config)
  }

  // 移除鼠标按下
  offMouseDown(callback) {
    this.remove(MOUSE_DOWN_KEY, callback)
  }

  // 注册鼠标抬起
  onMouseUp(callback, config) {
    this._cacheMouseEvent(MOUSE_UP_KEY, callback, this._mouseUpHandler.bind(this), config)
  }

  // 移除鼠标抬起
  offMouseUp(callback) {
    this.remove(MOUSE_UP_KEY, callback)
  }

  // 注册地图拖拽事件
  onDrag(callback, config = {}) {
    this._cacheMouseEvent(MOUSE_DRAG, callback, this._mouseMoveHandle.bind(this), {
      ...config,
      domEvent: MOUSE_MOVE_KEY
    })
  }

  // 移除拖拽事件
  offDrag(callback) {
    this.remove(MOUSE_DRAG, callback)
  }

  /**
   * 移除鼠标 onmousemove, onmousedown, onmouseup事件
   */
  removeMouseEvent() {
    if (this._view3d && functionExist(this._view3d, 'UnRegisterEvent')) {
      this._view3d.UnRegisterEvent()
    }
  }

  _resetMouseCallback() {
    for (let k of this.prefixAndCallbackMap.keys()) {
      this.prefixAndCallbackMap.delete(k)
    }
  }

  _cachePrefix(prefix) {
    if (!prefix || typeof prefix !== 'string') {
      return
    }

    let prefixArray = [prefix]

    for (let p of prefixArray) {
      this.prefix.add(p)
    }
  }

  _removePrefix(prefix = '') {
    if (!prefix || typeof prefix !== 'string') {
      return
    }

    let prefixArr = [prefix]

    for (let p of prefixArr) {
      this.prefix.delete(p)
    }

    // 移除完点选的前缀，需要重新绑定事件。才能真正移除
    this.reBind()
  }

  _getPrefix() {
    return Array.from(this.prefix).join(',')
  }

  _clearPrefix() {
    this.prefix = new Set()
  }

  _cacheCallback(prefix = '', callback, config = defaultConfig) {
    // prefix: '*', 'V', 'TEMP,PIC' 等形式
    if (!prefix || typeof prefix !== 'string') {
      return
    }

    let prefixArr = [prefix]

    // 根据前缀做缓存
    for (let k of prefixArr) {
      // 利用 Set 唯一性，避免回调多次重复的被执行
      const existCallbackMap = this.prefixAndCallbackMap.get(k)
      if (existCallbackMap) {
        if (existCallbackMap.has('callback')) {
          // const callbackSet = existCallbackMap.get('callback')
          const callbackMap = existCallbackMap.get('callback')
          const oneCallbackMap = new Map()
          oneCallbackMap.set('config', config)
          oneCallbackMap.set('callback', callback)

          // callbackSet.add(oneCallbackMap)
          let existCallbackSet
          if (config.isDouble) {
            existCallbackSet = callbackMap.get('double')
          } else {
            existCallbackSet = callbackMap.get('click')
          }
          existCallbackSet.add(oneCallbackMap)
        }
      } else {
        const prefixCallbackMap = new Map()
        const callbackConfigMap = new Map()
        const clickCallbackConfigSet = new Set()
        const doubleClickCallbackConfigSet = new Set()

        const oneCallbackMap = new Map()
        oneCallbackMap.set('config', config)
        oneCallbackMap.set('callback', callback)

        if (config.isDouble) {
          doubleClickCallbackConfigSet.add(oneCallbackMap)
        } else {
          clickCallbackConfigSet.add(oneCallbackMap)
        }

        // callbackConfigSet.add(oneCallbackMap)
        callbackConfigMap.set('click', clickCallbackConfigSet)
        callbackConfigMap.set('double', doubleClickCallbackConfigSet)

        prefixCallbackMap.set('prefix', k)
        prefixCallbackMap.set('callback', callbackConfigMap)
        this.prefixAndCallbackMap.set(k, prefixCallbackMap)
      }
    }
  }

  /**
   * 实际处理移除事件的逻辑，
   * @param oldPrefix
   * @param callback {Function} 移除事件回调时，必须传的。通过事件引用来查找对应的回调
   * @private
   */
  _removeCacheCallback(oldPrefix, callback) {
    if (!oldPrefix || typeof oldPrefix !== 'string') {
      return
    }

    let oldPrefixArr = oldPrefix.split(',')

    for (let k of oldPrefixArr) {
      if (this.prefixAndCallbackMap.has(k)) {
        const callbackMap = this.prefixAndCallbackMap.get(k)
        if (callbackMap) {
          // const oneCallbackSet = callbackMap.get('callback')
          const oneCallbackMap = callbackMap.get('callback')
          const oneClickCallbackSet = oneCallbackMap.get('click')
          const oneDoubleClickCallBackSet = oneCallbackMap.get('double')

          const removeCallback = (oneCallbackSet) => {
            oneCallbackSet.forEach((oneCallbackMap) => {
              const cachedCallback = oneCallbackMap.get('callback')
              if (mouseEventGroup.includes(k)) {
                if (oneCallbackMap.get('callback') === callback) {
                  oneCallbackSet.delete(oneCallbackMap)
                }
              } else {
                if (cachedCallback === callback) {
                  oneCallbackSet.delete(oneCallbackMap)
                }
              }
            })
          }

          if (oneClickCallbackSet.size > 0) {
            removeCallback(oneClickCallbackSet)
          }

          if (oneDoubleClickCallBackSet.size > 0) {
            removeCallback(oneDoubleClickCallBackSet)
          }

          if (
            oneClickCallbackSet.size === 0 &&
            oneDoubleClickCallBackSet.size === 0
          ) {
            this.prefixAndCallbackMap.delete(k)
            this._removePrefix(k)
          }
        }
      }
    }
  }

  _setMouseClickPrefix() {
    this.mapViewer.core.setParameters({
      // prefix: '*'
      prefix: this._getPrefix()
    })
  }

  _addMouseClickCallback() {
    const view3d = this._view3d
    const self = this

    this._setMouseClickPrefix()

    if (functionExist(view3d, 'SetMouseCallback')) {
      const handleClickCallback = (res) => {
        if (self._lastClickCallbackTimer) {
          clearTimeout(self._lastClickCallbackTimer)
        }

        self._lastClickCallbackTimer = setTimeout(() => {
          if (mapStatus === MAP_STATUS_DRAG) {
            // 拖动的时候，不响应点击事件
            return
          }

          if (self.debug) {
            console.log(res)
          }
          const gid = res.gid
          // 同时取 attr 里的GID， 因为有自己指定GID 无效，地图会自己生成 gid,为了还是能匹配上点击事件，
          const uuid = (res.attr && res.attr.gid) || ''
          const now = +new Date()
          if (gid || uuid) {
            let matchCallback = false
            const prefixMapArray = Array.from(
              this.prefixAndCallbackMap.entries()
            )

            const triggerCallback = (prefix, cachedCallbackSet) => {
              cachedCallbackSet.forEach((callbackConfigMap) => {
                const oneCallbackConfig = callbackConfigMap.get('config')
                const oneCallback = callbackConfigMap.get('callback')

                const {
                  compare,
                  isOnce
                  // prefix
                } = oneCallbackConfig

                // 第一个为 compare， 且属于用户自定义的比较函数
                if (compare !== FALSE_FUNC) {
                  // 调用用户指定的比较函数，传入地图 GID，和自己指定的 gid 给用户做判断
                  if (compare(gid, uuid)) {
                    const cleanup = oneCallback(res)
                    if (isFunction(cleanup)) {
                      self.cleanupCallback = cleanup
                    }
                    matchCallback = true
                  }
                } else {
                  if (
                    (gid &&
                      gid
                        .toLocaleLowerCase()
                        .startsWith(prefix.toLocaleLowerCase())) ||
                    (uuid &&
                      uuid
                        .toLocaleLowerCase()
                        .startsWith(prefix.toLocaleLowerCase())) ||
                    prefix === '*'
                  ) {
                    matchCallback = true
                    const cleanup = oneCallback(res)
                    if (isFunction(cleanup)) {
                      self.cleanupCallback = cleanup
                    }
                  }
                }
                // 根据每个回调的参数和 prefix 处理inOnce的情形
                if (isOnce) {
                  setTimeout(() => {
                    self.remove(prefix, oneCallback)
                  })
                }
              })
            }

            for (let i = 0; i < prefixMapArray.length; i++) {
              let [k, callbackMap] = prefixMapArray[i]
              const cachedPrefix = callbackMap.get('prefix')
              const cachedCallbackMap = callbackMap.get('callback')

              const clickCallbackSet = cachedCallbackMap.get('click') // 单击的回调
              const doubleCallbackSet = cachedCallbackMap.get('double') // 双击的回调

              // 只有单击的回调
              if (clickCallbackSet.size > 0 && doubleCallbackSet.size === 0) {
                triggerCallback(cachedPrefix, clickCallbackSet)

                // 只有双击的回调
              } else if (
                clickCallbackSet.size === 0 &&
                doubleCallbackSet.size > 0
              ) {
                if (now - self._lastClickTimestamp <= self._doubleClickTicker) {
                  triggerCallback(cachedPrefix, doubleCallbackSet)
                }

                // 既有单击，又有双击的回调. 如果在300ms内，没有触发同一个prefix的回调，
                // 则触发一次单击的回调。如果触发了，则触发双击的回调
              } else {
                let triggerDouble = false

                if (now - self._lastClickTimestamp <= self._doubleClickTicker) {
                  clearTimeout(self._clickCallbackTimer.get(cachedPrefix))
                  self._clickCallbackTimer.delete(cachedPrefix)
                  triggerDouble = true
                  triggerCallback(cachedPrefix, doubleCallbackSet)
                }

                if (!triggerDouble) {
                  const timerId = setTimeout(() => {
                    triggerCallback(cachedPrefix, clickCallbackSet)
                  }, self._doubleClickTicker)

                  self._clickCallbackTimer.set(cachedPrefix, timerId)
                }
              }
            }

            // 触发一次不匹配的回调，可以用来清除上一次回调里的事
            if (!matchCallback && isFunction(self.cleanupCallback)) {
              self.cleanupCallback()
              // 执行完毕，置空，避免重复执行
              self.cleanupCallback = null
            }
          } else {
            // 触发一次不匹配的回调，可以用来清除上一次回调里的事
            if (isFunction(self.cleanupCallback)) {
              self.cleanupCallback()
              // 执行完毕，置空，避免重复执行
              self.cleanupCallback = null
            }
          }
          self._lastClickTimestamp = now
        }, self._dragTicker)
        // }, 300)
      }
      view3d.SetMouseCallback(null)
      setTimeout(() => {
        view3d.SetMouseCallback(handleClickCallback)
      }, 10)
    }
  }

  /**
   * 监听鼠标的点击,反回点击点的位置
   * @param callback {Function}
   * @param isOnce {Boolean} 点击事件是否只响应一次
   */
  setMousePositionCallback(callback = NOOP, isOnce = true) {
    const view3d = this._view3d
    if (!isFunction(callback)) {
      logError(` callback must be a function, but got: ${typeof callback}.`)
      return
    }

    if (functionExist(view3d, 'SetMousePositionCallback')) {
      view3d.SetMousePositionCallback((position) => {
        callback(position)

        if (isOnce) {
          view3d.SetMousePositionCallback(null)
        }
      })
    }
  }

  /**
   * @description 监听指定前缀模型的点选事件
   * @param prefix {String | Function}
   * @param callback {Function | Boolean}
   * @param config {Object}
   * @param config.compare {Function}
   * @param config.isOnce {Boolean}
   * @param config.internal {Boolean}
   */
  onClick(prefix = '*', callback, config = defaultConfig) {
    config = Object.assign({}, defaultConfig, config)
    let { isOnce } = config
    if (isFunction(prefix)) {
      if (isBoolean(callback)) {
        isOnce = callback
      }
      callback = prefix
    }

    if (!isFunction(callback)) {
      logError(` callback must be a function, but got: ${typeof callback}.`)
      return
    }

    // 缓存 prefix
    this._cachePrefix(prefix)

    // 缓存回调
    this._cacheCallback(prefix, callback, config)

    this._addMouseClickCallback()
  }

  /**
   * 重新绑定事件
   */
  reBind() {
    this._addMouseClickCallback()
    // this._setMouseClickPrefix()
  }

  /**
   * @description 设置双击事件
   * @function
   * @param prefix
   * @param callback
   * @param config
   */
  onDoubleClick(prefix = '*', callback, config) {
    // const self = this
    this.onClick(
      prefix,
      // (res) => {
      // const now = +new Date()
      // // 两个点击如果间隔在100毫秒以内， 响应一次双击事件即可
      // if (now - self._lastClickTimestamp <= 300) {
      //   callback(res)
      // }
      // self._lastClickTimestamp = now
      // },
      callback,
      {
        ...config,
        isDouble: true,
        prefix
      }
    )
  }

  /**
   * 移除指定前缀的鼠标事件
   * @param prefix {String}
   * @param callback {Function}
   */
  remove(prefix, callback) {
    if (!prefix) {
      warn(`prefix is not defined, or is ''.`)
      return
    }
    // 地图容器上的鼠标事件要单独处理, 因为要移除DOM上的事件
    if (mouseEventGroup.includes(prefix)) {
      this._removeMouseEvent(prefix, callback)
    } else {
      this._removeCacheCallback(prefix, callback)
    }
  }

  removeMousePositionCallback() {
    const view3d = this._view3d

    if (functionExist(view3d, 'SetMousePositionCallback')) {
      view3d.SetMousePositionCallback(null)
    }
  }

  /**
   * 移除添加的所有前缀的点击事件
   */
  clear() {
    const view3d = this._view3d
    this.mapViewer.core.setParameters(null)
    if (functionExist(view3d, 'SetMouseCallback')) {
      this._clearPrefix()
      view3d.SetMouseCallback(null)
      this._resetMouseCallback()
    }
  }

  /**
   * 移除鼠标点击事件的监听,但保留已注册的事件回调。后续可通过 reBind 来恢复事件的执行
   */
  silent() {
    const view3d = this._view3d
    view3d.Clear()
    view3d.SetParameters(null)
    view3d.SetMouseCallback(null)
  }
}

export default MapEvent
