import { CATEGORY_DOOR } from '../../config'
import ModelBase from '../../base'

class Intercom extends ModelBase {
  static mapViewer = null
  static model = '$INTERCOM'
  static icon = '$ICON_INTERCOM'

  static _getModelInstance = (entity) => {
    if (!entity) {
      return null
    }

    if (!Intercom.mapViewer) {
      return
    }

    const model = Intercom.mapViewer.model
    const adapter = model.getDeviceAdapter()
    const modelSelector = adapter.getSelector()
    return modelSelector.selectEntityById(entity.attr.id)
  }

  // 模型点击事件实例的处理函数
  static modelClickHandler = (entity) => {
    console.log(entity)

    const modelInstance = Intercom._getModelInstance(entity)
    if (modelInstance) {
      modelInstance.flyTo()
    }
  }

  // 模型双击击事件实例的处理函数
  static modelDoubleClickHandler = (entity) => {
    console.log(entity)

    const modelInstance = Intercom._getModelInstance(entity)
    if (modelInstance) {
      modelInstance.flyTo()
    }
  }

  // 图标点击事件的处理函数
  static iconClickHandler = (entity) => {
    console.log(entity)

    const modelInstance = Intercom._getModelInstance(entity)
    if (modelInstance) {
      modelInstance.flyTo()
    }
  }

  // 图标双击事件的处理函数
  static iconDoubleClickHandler = (entity) => {
    console.log(entity)

    const modelInstance = Intercom._getModelInstance(entity)
    if (modelInstance) {
      modelInstance.flyTo()
    }
  }

  constructor(mapViewer, ...props) {
    super(mapViewer, ...props)
    Intercom.mapViewer = mapViewer
    this.category = Intercom.model
    this.categoryName = '对讲'
    this.type = CATEGORY_DOOR
    this.typeName = '对讲'
    this.fileName = 'duijiang'

    this.create()
  }

  create() {
    const self = this
    const config = this.config
    const data = this.data || {}
    const drawer = this.mapViewer.drawer
    const event = this.mapViewer.event

    // 模型
    const modelEntity = drawer.config.model({
      attr: {
        id: data.id
      },
      gid: this._concatGid(Intercom.model, data.id),
      fileName: this.fileName,
      scale: 1,
      location: data?.options?.location || null
    })

    // 图标
    const imageEntity = drawer.config.image({
      attr: {
        id: data.id
      },
      gid: this._concatGid(Intercom.icon, data.id),
      style: this.fileName + '_icon',
      location: self.getIconLocation(modelEntity?.location)
    })

    if (config.draw) {
      // setMousePositionCallback 默认只触发一次
      event.setMousePositionCallback((location) => {
        modelEntity.location = location
        imageEntity.location = self.getIconLocation(location)

        //  绘制成功后,额外添加图标
        drawer.create.image(imageEntity)

        drawer.create.model(modelEntity, true).then((modelAttr) => {
          config.onSuccess(modelAttr)
        })

        self.setModel(modelEntity)
        self.setIcon(imageEntity)

        this.markVisible(true)
      })
    } else {
      self.setModel(modelEntity)
      self.setIcon(imageEntity)

      // 是否上图
      if (config.onMap) {
        drawer.create.model(modelEntity)
        drawer.create.image(imageEntity, true)

        this.markVisible(true)
      }
    }
  }

  /**
   * 门禁定位
   * @override
   * @return {Intercom}
   */
  flyTo(config = { speed: 40, showLocation: false }) {
    super.flyTo(config)
    return this
  }

  // /**
  //  * 门禁高亮
  //  * @override
  //  */
  // highlight() {
  //   const entity = this.getModel()
  //   const mapViewer = this.mapViewer
  //   this.clearHighlight().then(() => {
  //     if (entity) {
  //       // 目前只高亮模型即可
  //       mapViewer.drawer.setObjectHighlight(entity?.gid)
  //     }
  //   })
  //
  //   return this
  // }

  /**
   * 属性的更新，包括位置，配置项，数据
   * modelInstance.update({
   *   data: cameraProps,
   *   // 位置变化了
   *   changes: newCameraProps.options.location
   * })
   * @param {Object} updater
   * @param {Object} updater.changes  特指模型位置的更新
   * @param {Object} updater.data 特指与模型关联的数据更新
   * @return {Intercom}
   */
  update(updater = {}) {
    super.update(updater)
    return this
  }

  /**
   * @override 设置与整个实例关联的所有实体的可见性
   * @param visible
   * @return {Intercom}
   */
  setVisible(visible) {
    super.setVisible(visible)
    return this
  }
}

export default Intercom
