import ModelBase from './base'
import { TYPE } from './config'
import {
  CAMERA_TYPE_BOX,
  CAMERA_TYPE_DOME,
  CAMERA_TYPE_SPEED_DOME,
  CATEGORY_BAYONET,
  CATEGORY_CAMERA,
  CATEGORY_DOOR,
  CATEGORY_DOOR_HK_FACE_ACCESS_TERMINAL,
  CATEGORY_FACE,
  CATEGORY_INTERCOM,
  CATEGORY_INTERCOM_HK_INDOOR,
  CATEGORY_INTERCOM_HK_OUTDOOR
} from './config'

// 监控类
import Camera from './devices/camera'
import CameraBox from './devices/camera/CameraBox'
import CameraSpeedDome from './devices/camera/CameraSpeedDome'
import CameraDome from './devices/camera/CameraDome'
import CameraFace from './devices/camera/CameraFace'
import CameraBayonet from './devices/camera/CameraBayonet'

// 门禁类
import Door from './devices/door'
import DoorHKFaceAccessTerminal from './devices/door/DoorHKFaceAccessTerminal'

// 对讲类
import Intercom from './devices/intercom'
import IntercomHkIndoor from './devices/intercom/IntercomHkIndoor'
import IntercomHkOutdoor from './devices/intercom/IntercomHkOutdoor'

// 其它地图实体类
import MapModel from './model'
import MapText from './text'
import MapImage from './image'

const getModelClass = (data) => {
  let ClassName = ModelBase
  // categoryId 是针对设备类的
  const categoryId = data.category_id

  // type 是针对通用地图模型类的，所以需要每一个地图实体都有自己的类实现
  const type = data.type

  if (categoryId !== undefined) {
    switch (categoryId) {
      // 摄像头
      case CATEGORY_CAMERA:
        // 如果数据有问题默认这里就指定为Camera的实例
        ClassName = Camera
        break

      case CAMERA_TYPE_BOX:
        // 枪机 (Box Camera)
        ClassName = CameraBox
        break

      case CAMERA_TYPE_SPEED_DOME:
        // 球机 (Speed Dome Camera)
        ClassName = CameraSpeedDome
        break

      case CAMERA_TYPE_DOME:
        // 半球 (Dome Camera)
        ClassName = CameraDome
        break

      case CATEGORY_FACE:
        // 人脸
        ClassName = CameraFace
        break

      case CATEGORY_BAYONET:
        // 卡口
        ClassName = CameraBayonet
        break

      // 门禁
      case CATEGORY_DOOR:
        ClassName = Door
        break

      // 对讲
      case CATEGORY_INTERCOM:
        ClassName = Intercom
        break

      // 海康对讲室内终端
      case CATEGORY_INTERCOM_HK_INDOOR:
        ClassName = IntercomHkIndoor
        break

      // 海康对讲室外终端
      case CATEGORY_INTERCOM_HK_OUTDOOR:
        ClassName = IntercomHkOutdoor
        break

      // 门禁 - 海康人脸识别一体机
      case CATEGORY_DOOR_HK_FACE_ACCESS_TERMINAL:
        ClassName = DoorHKFaceAccessTerminal
        break

      default:
    }
  } else if (type !== undefined) {
    switch (type) {
      case TYPE.image.typeName:
        ClassName = MapImage
        break
    }
  }

  return ClassName
}

export default getModelClass
