// import { utils } from '@ty/shared'
// import Camera from '../category/devices/camera'
import * as utils from '../../utils'
const { isFunction } = utils

// 事件的配置选项参数
export const defaultEventConfig = {
  // 指定是否一次性的事件
  isOnce: false,

  // 这个是全局的配置。某一类事件，有一个回调指定了 defaultAction 会影响到所有注册的回调
  // 是否执行默认的操作, 比如。监控的点击，默认会根据相机的视角进行跳转，传 false 来阻止默认的行为
  defaultAction: true
}

/**
 * @class
 * @classdesc 模型事件的基类
 *            1. 定义事件的注册机制。保证各事件都只注册一次。
 *            2. 定义各类设备通用的事件集合。
 *             onClick: 模型点击
 *             onDoubleClick: 模型双击
 *             onIconClick: 图标点击
 *             onIconDoubleClick: 图标双击
 *             各设备还可以扩展自己的实现
 *
 *             TODO：实现可以阻止某一类设备，某一个事件的默认行为
 *
 *           3. 关于事件注册的说明：
 *              子类应该定义自己的事件类型命名空间。以防止不同类型事件因为事件类型名称相同而冲突
 */
export class EventBase {
  constructor(mapViewer) {
    this.mapViewer = mapViewer

    // 事件队列
    this.events = new Map()

    // 每一类设备都配一个默认的配置项
    this.config = { ...defaultEventConfig }

    //* 注意，子类需要自己调用注册事件的函数: this.listen()
    //* 原因在于。this 指向在回调中需要绑定到子类
  }

  /**
   * 全局配置的
   */
  config(option = defaultEventConfig) {
    this.config = {
      ...defaultEventConfig,
      ...option
    }
  }

  /**
   * 获取某一类设备的全局配置
   * @param key
   */
  getConfig(key) {
    const eventMap = this.events.get(key)

    if (eventMap) {
      return eventMap.get('config')
    }
    return null
  }

  /**
   * @desc 事件订阅
   * @param key
   * @param callback
   * @param config
   */
  subscribe(key, callback, config = defaultEventConfig) {
    /**
     * events 的结构为：
     *  {
     *    key: {
     *      config: {} // global config,
     *      queue: [
     *      {
     *        callback: callback,
     *        config, config
     *      }
     *    ]
     *    }
     *  }
     *
     */
    const cachedEventMap = this.events.get(key)

    const callbackMap = new Map()
    callbackMap.set('callback', callback)
    callbackMap.set('config', config)

    if (cachedEventMap) {
      const eventQueue = cachedEventMap.get('queue')
      eventQueue.add(callbackMap)
      // 更新某个事件类的全局配置
      cachedEventMap.set('config', config)
    } else {
      const eventMap = new Map()
      const eventQueue = new Set()
      eventQueue.add(callbackMap)
      eventMap.set('queue', eventQueue)
      eventMap.set('config', config)
      this.events.set(key, eventMap)
    }
  }

  /**
   * 移除事件订阅 - TODO: 待验证正确性
   * @param {String} key 指定要移除的是什么类型的事件
   * @param {Function} callback
   */
  unsubscribe(key, callback) {
    const eventMap = this.events.get(key)
    if (eventMap) {
      const eventQueue = eventMap.get('queue')
      for (let eventMap of eventQueue.values()) {
        // 从回调队列中，移除这个回调
        if (eventMap.get('callback') === callback) {
          eventQueue.delete(eventMap)
        }
      }

      // 如果这一类事件的回调队列空了，直接移除这一类事件的队列
      if (eventQueue.size === 0) {
        this.events.delete(key)
      }
    }
  }

  /**
   * @desc 事件分发
   * @param key
   * @param args
   */
  dispatch(key, ...args) {
    const eventMap = this.events.get(key)

    if (eventMap) {
      const eventQueue = eventMap.get('queue')
      // const globalConfig = eventMap.get('config')
      for (let eventMapItem of eventQueue.values()) {
        const callback = eventMapItem.get('callback')
        const callbackConfig = eventMapItem.get('config')
        if (isFunction(callback)) {
          callback(...args)
        }

        // 对于只需要触发一次的事件，调用后移除掉
        if (callbackConfig && callbackConfig.isOnce) {
          this.unsubscribe(key, callback)
        }
      }
    }
  }

  /**
   * @desc 事件初始化的抽象方法 - 需要各类设备自己实现。因为各类设备的前缀只有自己知道
   * @abstract
   */
  listen = () => {}

  /**
   * @desc 清空所有监听的事件
   // * @abstract
   */
  clear() {
    // 取消事件订阅
    // this.unsubscribe()

    // 清空回调列表
    this.events.clear()
  }

  /**
   * @abstract
   * @optional
   * @param callback
   * @param config
   */
  onClick(callback, config = defaultEventConfig) {}

  /**
   * @abstract
   * @optional
   * @param callback
   */
  offClick(callback) {}

  /**
   * @abstract
   * @optional
   * @param callback
   * @param config
   */
  onDoubleClick(callback, config = defaultEventConfig) {}

  /**
   * @abstract
   * @optional
   * @param callback
   */
  offDoubleClick(callback) {}

  /**
   * @abstract
   * @optional
   * @param callback
   * @param config
   */
  onIconClick(callback, config = defaultEventConfig) {}

  /**
   * @abstract
   * @optional
   * @param callback
   */
  offIconClick(callback) {}

  /**
   * @abstract
   * @optional
   * @param callback
   * @param config
   */
  onIconDoubleClick(callback, config = defaultEventConfig) {}

  /**
   * @abstract
   * @optional
   * @param callback
   */
  offIconDoubleClick(callback) {}

  /**
   * 鼠标移入
   */
  onMouseIn() {

  }

  /**
   * 鼠标移出
   */
  onMouseOut() {

  }

  /**
   * @desc 模型的点击事件回调， 需要子类自己实现
   * @abstract
   * @private
   */
  _modelClickHandler() {}

  /**
   * @desc 模型的双击事件回调， 需要子类自己实现
   * @abstract
   * @private
   */
  _modelDoubleClickHandler() {}

  /**
   * @desc 图标的单击事件回调， 需要子类自己实现
   * @abstract
   * @private
   */
  _iconClickHandler() {}

  /**
   * @desc 图标的双击击事件回调， 需要子类自己实现
   * @abstract
   * @private
   */
  _iconDoubleClickHandler() {}
}
