import { modelOptions } from '../options'
import getModelClass from './getClass'

/**
 * 模型工厂，负责生产模型实例
 * @factory
 */
class Model {
  constructor(mapViewer) {
    this.mapViewer = mapViewer
  }

  create(data, config = modelOptions) {
    let ClassName = getModelClass(data)
    if (ClassName) {
      return new ClassName(this.mapViewer, data, config)
    }

    console.warn(`device category of ${data.categoryId}, is not support yet.`)
  }
}

export default Model
