/**
 * 初始视角
 */
// import { utils } from '@ty/shared'
import * as utils from '../utils'
import { functionExist } from '../error'
import { schedule, waitSchedule } from '../scheduler'

const { isPlainObject, isFunction, NOOP } = utils

class MapCamera {
  constructor(view3d, viewer) {
    this.view3d = view3d
    this.viewer = viewer

    // 视角移动时，默认的速度设置。 有关视角移动的方法中，如果没有指定duration参数，会统一使用默认的速度设置
    // 默认 3 秒即可
    this.duration = 1

    // 每次定位过程中，都设置一个定时器，以免回调不过不执行，在定时器里执行回调
    this.timer = null
  }

  init() {
    this.resetHome()
  }

  /**
   * 重置初始视角
   */
  resetHome() {
    let view3d = this.view3d
    if (functionExist(view3d, 'ResetHome')) {
      view3d.ResetHome()
    }
  }

  /**
   * 设置相机视口位置
   * @param position {Object}
   */
  setPosition(position) {
    const view3d = this.view3d
    if (isPlainObject(position)) {
      if (functionExist(view3d, 'SetPosition')) {
        view3d.SetPosition(position)
      }
    }
  }

  /**
   * 动态定位到指定点
   * @param position {Object}
   * @param durationTime {Number} 移动的时间
   * @param showLocation {Boolean} 是否显示默认的位置图标
   * @param enableCurve {Boolean} 是否启用抛物线移动效果， 默认 false. 目前视角的跳转可以正确适应 pitch, yaw, roll 的变化，且是平滑的抛物轨迹，这个参数直接传 false 即可
   * @param callback {Function} 定位到终点的回调，这个回调会稍微提前一点触发。
   */
  flyToPosition(
    position,
    showLocation = false,
    durationTime = this.duration,
    enableCurve = false,
    callback = NOOP
  ) {
    const view3d = this.view3d
    // 定位之前，显示遮罩
    this.viewer?.core?.showCover()
    // 先删除点选回调
    this.viewer?.event?.silent()
    const self = this

    if (this.timer) {
      clearTimeout(this.timer)
    }

    // 定位结束后，隐藏遮罩，重新绑定事件
    const onFinish = (arg) => {
      self.viewer?.core?.hideCover()
      self.viewer?.event?.reBind()

      if (isFunction(callback)) {
        // 延迟一点点执行，因为定位的回调执行的时机会早于视角移动到最后
        setTimeout(() => {
          callback(arg)
        }, 100)
      }
    }

    if (!position) {
      onFinish()
      return
    }

    // 如果是单个坐标点，直接做定位
    if (isPlainObject(position)) {
      if (functionExist(view3d, 'FlyToPosition')) {
        view3d.FlyToPosition(position, showLocation, durationTime, enableCurve, res => {
          if (self.timer) {
            clearTimeout(self.timer)
            self.timer = null
          }
          onFinish(res)
        })

        this.timer = setTimeout(() => {
          onFinish(null)
          // timer 的执行比回调稍微晚一点
        }, (durationTime + 0.5) * 1000)
      }

      // 如果传入一个点位数组，说明是一条自定义的轨迹线
    } else if (Array.isArray(position)) {
      let index = 0
      let tempTimer = null
      const execFly = () => {
        const currentPosition = position[index]
        // 允许每个点位控制自己定位的时间
        let duration = currentPosition.duration || durationTime

        const currentFlyEnd = (res) => {
          if (tempTimer) {
            clearTimeout(tempTimer)
          }

          index++
          // 到最后一个点了，执行结束的回调
          if (index === position.length) {
            tempTimer = null
            onFinish(res)
          } else {
            execFly()
          }
        }
        view3d.FlyToPosition(currentPosition, showLocation, duration, enableCurve, currentFlyEnd)

        // 使用定时器来保证中间点也能有回调
        tempTimer = setTimeout(currentFlyEnd, (duration + 0.2) * 1000)
      }

      execFly()
    }
  }

  /**
   * 动态定位到指定点 - 传一个参数的形式
   * @param options.position {Object | Array} 目标位置，对象，或者数组，数组标识由多个点组成的轨迹
   * @param options.duration {Number} 定位持续时间，0 - 10，单位： 秒
   * @param options.enableCurve {Boolean} 是否启用抛物线效果
   * @param options.showLocation {Boolean} 是否使用默认图标，显示终点位置
   * @param options.onFinish {Function} 定位结束后的回调
   */
  flyToPositionByOptions(options = {}) {
    const defaultOptions = {
      // 目标位置
      position: null,

      // 定位持续时间
      duration: 1,

      // 是否启用抛物线效果
      enableCurve: false,

      // 是否在终点显示图标 - 一般不需要设置，不显示即可，如需要显示定位的位置，可以手动添加图标
      showLocation: false,

      // 定位结束后的回调
      onFinish: NOOP
    }

    const flyOption = Object.assign({}, defaultOptions, options)
    this.flyToPosition(
      flyOption.position,
      flyOption.showLocation,
      flyOption.duration,
      flyOption.enableCurve,
      flyOption.onFinish
    )
  }

  /**
   * 视角跳转到指定对象
   * @param obj {Object}
   * @param duration {Number} 飞行时间，单位：秒 1—10  最大值10
   * @param showLocation {Boolean}
   */
  flyToObject(obj, duration = this.duration, showLocation = false) {
    const view3d = this.view3d
    if (
      isPlainObject(obj) &&
      !!obj.gid &&
      functionExist(view3d, 'FlyToObject')
    ) {
      view3d.FlyToObject(obj, showLocation, duration)
    }
  }

  /**
   * 根据对象的GID跳转到指定对象
   * @param {Object} options
   */
  flyToObjectById(options = {}) {
    const defaultOptions = {
      gid: '',
      showLocation: false,
      pitch: 90,
      yaw: 0,    // 偏航角 0 - 360或 -180 - 180
      distance: null,
      duration: this.duration,
      enableCurve: false,
      onFinish: () => null
    }
    options = { ...defaultOptions, ...options }

    if (!options.gid) {
      console.warn('object gid is undefined or \'\'.')
      return
    }
    const view3d = this.view3d
    if (functionExist(view3d, 'FlyToObjectById')) {
      view3d.FlyToObjectById(
        options.gid,
        options.showLocation,
        options.pitch,
        options.distance,
        options.duration
      )
    }
  }

  /**
   * 地图指北
   */
  setNorth() {
    const view3d = this.view3d
    if (functionExist(view3d, 'SetNorth')) {
      view3d.SetNorth()
    }
  }

  /**
   * 获取初始位置点
   * @return {Promise<Object|null>}
   */
  getHomePosition() {
    const view3d = this.view3d
    if (functionExist(view3d, 'GetHomePosition')) {
      return schedule(view3d, view3d.GetHomePosition)
    }
  }

  /**
   * 获取当前视口的位置
   * @return{Promise<Object|null>}
   */
  getCurrentPosition() {
    const view3d = this.view3d
    if (functionExist(view3d, 'GetCurrentPosition')) {
      return schedule(view3d, view3d.GetCurrentPosition)
    }
  }

  /**
   * 获取对象的屏幕坐标
   *
   */
  getScreenPosition(object) {
    const view3d = this.view3d
    if (functionExist(view3d, 'GetScreenPosition')) {
      return schedule(view3d, view3d.GetScrrenPosition, object)
    }
  }

  /**
   * 新增@2022-04-29
   * @test todo
   * @param options 定位参数
   */
  flyToObjectByType(options = {}) {
    const defaultOptions = {
      // TODO：地图中，所有对象对应的类型待补充。
      type: 10102, // 定位的类型
      pitch: 90, // 俯视角
      yaw: 0,    // 偏航角 0 - 360或 -180 - 180
      distance: 100, // 相机距离目标的距离，单位 米
      duration: this.duration,
      heightRadio: 1.0, // 高度比例， 默认 1.0
      enableCurve: false,
      onFinish: NOOP
    }

    options = { ...defaultOptions, ...options }
    const view3d = this.view3d
    if (functionExist(view3d, 'FlyToObjectByType')) {
      return waitSchedule(
        10,
        view3d,
        view3d.FlyToObjectByType,
        ...options
      )
    }
  }

  /**
   * 新增@2022-04-29
   * @test todo
   * @param options {Object}
   */
  flyToObjectByIds(options) {
    const defaultOptions = {
      ids: [], // 多个对象的 GID 集合
      pitch: 90, // 俯视角
      yaw: 0,    // 偏航角 0 - 360或 -180 - 180
      distance: 100, // 相机距离目标的距离，单位 米
      duration: this.duration,
      heightRadio: 1.0, // 高度比例， 默认 1.0
      enableCurve: false,
      onFinish: NOOP
    }

    options = { ...defaultOptions, ...options }
    const view3d = this.view3d
    if (functionExist(view3d, 'FlyToObjectByIds')) {
      return waitSchedule(
        10,
        view3d,
        view3d.FlyToObjectByIds,
        ...options
      )
    }
  }

  async zoomIn() {
    const view3d = this.view3d
    if (functionExist(view3d, 'ZoomIn')) {
      return waitSchedule(10, view3d, view3d.ZoomIn)
    }
  }

  async zoomOut() {
    const view3d = this.view3d
    if (functionExist(view3d, 'ZoomOut')) {
      return waitSchedule(10, view3d, view3d.ZoomOut)
    }
  }

  async setPitchAngle(val) {
    const view3d = this.view3d
    if (functionExist(view3d, 'SetPitchAngle')) {
      return waitSchedule(100, view3d, view3d.SetPitchAngle, val)
    }
  }

  async getPitchAngle(val) {
    const view3d = this.view3d
    if (functionExist(view3d, 'GetPitchAngle')) {
      return schedule(view3d, view3d.GetPitchAngle)
    }
  }

  async setRotateAngle(val) {
    const view3d = this.view3d
    if (functionExist(view3d, 'SetRotateAngle')) {
      return waitSchedule(100, view3d, view3d.SetRotateAngle, val)
    }
  }

  async getRotateAngle(val) {
    const view3d = this.view3d
    if (functionExist(view3d, 'GetRotateAngle')) {
      return schedule(view3d, view3d.GetRotateAngle)
    }
  }

  /**
   * 围绕对象旋转
   * @todo: test
   * @param options {Object}
   */
  rotate(options = {}) {
    const defaultOptions = {
      gid: '',
      pitch: 90,
      radio: 1.0,
      distance: 10, // 可视距离
      highlight: true // 对象是否需要高亮
    }
    options = { ...defaultOptions, ...options }
    const view3d = this.view3d
    if (functionExist(view3d, 'Rotate')) {
      view3d.Rotate(
        options.pitch,
        options.radio,
        options.distance,
        options.gid,
        options.highlight
      )
    }
  }
}

export default MapCamera
