import { CATEGORY_FACE } from '../../config'
import Camera from './index'

/**
 * 人脸
 */
class CameraFace extends Camera {
  constructor(mapViewer, ...props) {
    super(mapViewer, ...props)
    this.category = 'CAMERA' // 设备类别的名称.也用做GID的前缀
    this.categoryName = '监控'
    this.type = CATEGORY_FACE
    this.typeName = '人脸'
    // TODO: 人脸的模型名称可能不是枪机,这里先用枪机
    this.fileName = 'qiangji'

    this.create()
  }
}

export default CameraFace
