// import { utils } from '@ty/shared'
import * as utils from '../utils'
const { NOOP } = utils

// 添加多个模型时的选项配置
export const batchModelOptions = {
  // 是否初次加载数据
  isFirst: false,

  // 一次添加的数量，不要超过200个. 默认200个
  size: 200,

  // 是否直接上图 - 默认ture
  onMap: true,

  // 是否对每个数据项实例化
  instance: true,

  // 所有模型都添加完的回调
  onSuccess: NOOP,

  // progress 回调
  onProgress: NOOP,

  // 某次出错的回调
  onError: NOOP
}

// TODO: 每类设备都可能又不同的配置项
export const modelOptions = {
  // 上图方式 - 是普通的上图,还是绘制上图
  // 普通上图指调用 OverLayerCreatObject
  // 绘制上图指调用 OverLayerStartEdit
  draw: false,

  // 是否上图 - 默认true. 指的是与设备和与设备关联的所有模型属性（名字，图标等是否显示）
  onMap: true,

  // 整体的缩放比例
  scale: 1,

  // 图标相关配置
  // 是否附加图片
  image: true,

  // 图片是否跟随滚轮进行缩放，默认是固定大小的
  imageScale: 1,

  // 是否显示图标
  imageVisible: true,

  // 模型相关配置
  modelVisible: true,

  modelScale: 1,

  // 文本相关配置
  textVisible: true,

  textScale: 1,

  // TODO：指定只有当相机高度小于设定值时才显示，模型、图标,
  //  这里应该要考虑不同坐标系（经纬度和笛卡尔坐标）或者 是坐标系中的单位：厘米，米等因素，
  //  所以最好有个全局设置。
  visibleDistance: 100000,

  // TODO：是否在当前可视范围内进行显示
  visibleInViewBox: false,

  onSuccess: NOOP,

  onError: NOOP,

  // 在每个设备的可见状态发送变化时触发。可见状态包括：当前相机视角超出/达到指定的高度，或者，进入/离开可视范围
  onVisibleStatusChange: NOOP,

  // 鼠标移动相关的
  onMouseMove: NOOP,
  onMouseMoveOut: NOOP
}
