﻿//工具类
import * as utils from './utils'

//===========框架基本信息=========
import * as ver from './version'

// //核心类
import MapCore from './core'

//相机类
import MapCamera from './camera'

import MapDrawer from './drawer'

import ModelManager from './model'

import * as config from './model/category/config'

// 建筑类
import MapBuild from './build'

// 新的建筑实例
import Build from './build/build'

import MapEvent from './event'

// 异步任务调度器
import * as scheduler from './scheduler'

import * as mapError from './error'

const { isFunction, NOOP } = utils

/**
 * 默认参数
 */
const defaultOption = {
  debug: false,

  id: 'mapv3d-container',
  url: '',
  projectId: '',
  token: '',
  // 新版本的 mapvision3d.min.js 初始化地图的 token 使用 mapkay 作为键名
  mapkey: '',
  manual: false, // 是否自动创建地图，允许延迟初始化地图
  loading: true, //是否展示初始 loading 状态
  northControl: false, // 地图指北针控件是否显示
  enableKeyboard: true, // 是否启用键盘
  enableMouse: true, // 是否启用鼠标
  defaultLocate: true, // 是否允许默认定位

  // 是否在窗口 resize 后，默认设置地图的分辨率
  autoResize: true,

  // resize 的防抖间隔，用来避免 resize 后，retResolution 过于频繁。
  autoResizeTicker: 300,

  // 是否在加载地图成功后，立即设置一次地图分辨率
  defaultSetResolution: false,

  // 设置的地图分辨率的倍率，默认会获取地图容器的宽高，乘以默认的倍率(如果是手机端，还会乘以屏幕的像素比)
  resolutionScale: 1,

  //  是否默认重置建筑的状态(比如分层)
  resetBuildings:  true,

  // 内置的鼠标图标是否可见
  internalMouseVisible: false,

  complete: NOOP, // 地图创建完成回调
  error: NOOP, // 地图创建失败回调
}

class MapViewer {
  constructor(config) {
    this._initFinished = false
    // MapCore 里，不引入其它模块，因为初始化时，其它模块还没有准备就绪，
    // 所以，MapCore 里直接通过调用 view3d 上的底层方法
    this.core = new MapCore()
    this.config = Object.assign({}, defaultOption, config)
    if (!this.config.manual) {
      this._init()
    }
    this.event = null
    this.build = null
    this.MapBuild = null
    this.camera = null
    this.drawer = null
    this.model = null
    this.scheduler = scheduler

    //打印信息
    ver.printVersion()
  }

  // 手动初始化时，要调用这个方法
  create() {
    if (this._initFinished) {
      mapError.warn(`MapViewer is already initialize.`)
      return
    }

    this._init()
  }

  // 实际去初始化地图，并绑定实例对象
  _init() {
    this.core.createMap(this.config).then(() => {
      let view3d = this.core.view3d
      if (view3d) {
        this.event = new MapEvent(view3d, this)
        this.build = new MapBuild(view3d, this)
        this.mapBuild = new Build(this)
        this.camera = new MapCamera(view3d, this)
        this.drawer = new MapDrawer(view3d, this)
        this.model = new ModelManager(view3d, this)

        this._initFinished = true

        // 加载完成事件
        if (this.config.complete && isFunction(this.config.complete)) {
          this.config.complete(this, view3d)
        }
      }
    })
  }
}

let name = 'MapV3D 三维地球框架'
let website = 'http://www.aimapvision.com/'
let author = '图洋科技'
let version = ver.getVersion()
let update = ver.getBuildTime()
let env = ver.environment

const CATEGORY = config

export {
  name,
  website,
  author,
  version,
  update,
  env,
  mapError as error,
  MapViewer,
  scheduler,
  utils,
  CATEGORY
}
