// import { utils } from '@ty/shared'
import * as utils from '../../utils'

const { createUUID } = utils

export const MODEL_INSTANCE_TYPE = 'MODEL_INSTANCE_TYPE'

// 模型对象基类
class ModelBase {
  static CATEGORY = 'MODEL'

  constructor(mapViewer, data, config) {
    // 内部实例的类型，用来需要实例化后的类实例和普通对象
    this.$$typeof = MODEL_INSTANCE_TYPE
    this.config = config // 创建时，自定义的配置项
    this.data = null // 与模型关联的具体数据
    this.id = null // 模型的唯一标识
    // 用 Map 保存与这个模型类关联的所有实体
    this.entity = new Map() // 模型上图后的地图实体属性 map
    this.type = data.category_id
    this.typeName = '模型基类'

    // 实例模型是否可见
    this.visible = false

    // 是否添加到地图
    this.created = false

    this.mapViewer = mapViewer

    this.setData(data)
  }

  _concatGid(prefix = createUUID(), id = createUUID()) {
    return `${prefix}_${id}`
  }

  setState(key, value) {
    this[key] = value
    // 合并属性更新
  }

  // 返回原始的数据
  getData() {
    return this.data
  }

  setData(data) {
    if (data) {
      this.data = data
      this.id = data.id
    }
  }

  // 返回实例的 entity Map
  getEntity(key) {
    if (key) {
      return this.entity.get(key)
    }
    return this.entity
  }

  // 返回当前实例所有关联的 entity
  getAllEntities() {
    return Array.from(this.entity.values())
  }

  /**
   * 返回实例所有的地图实例数据列表
   * @returns {unknown[]}
   */
  getEntityData() {
    return Array.from(this.entity.values()).map((v) => v.data)
  }

  /**
   * 保存与模型关联的实体属性
   * @param key 不同的实体可以通过不同的 key 来区分，
   *            定义如下：
   *                模型： model
   *                图标： icon
   *                可视区域： area - 监控具有
   *                其它： 待补充
   * @param props 每一类实体的数据格式定义如下：
   *              {
   *                gid: entity 上图后的 gid
   *                type: entity 的类型
   *                data: 实际上图后地图返回的entity数据，可以直接用来上图
   *                visible: 表明当前实体是否绘制上图了
   *              }
   */
  setEntity(key, props) {
    this.entity.set(key, {
      gid: props.gid || props.attr.id,
      type: props.type,
      data: props,
      visible: props.visible
    })
  }

  deleteEntity(key) {
    this.entity.delete(key)
  }

  getModel() {
    return this.entity.get('model')
  }

  getIcon() {
    return this.entity.get('icon')
  }

  setModel(props) {
    this.setEntity('model', props)
  }

  setIcon(props) {
    this.setEntity('icon', props)
  }

  /**
   * 定义图标的位置，总是在模型位置上方固定距离处
   * @param location
   */
  getIconLocation(location) {
    if (!location) {
      return null
    }
    return {
      ...location,
      z: location.z + 20
    }
  }

  /**
   * 设置整体实例的所有实体的可见性。需要各个实例自己实现
   * 只要 模型 完成上图， 模型实例的 visible 属性即为 true。
   * @abstract
   */
  setVisible(visible) {
    const drawer = this.mapViewer.drawer

    // 不重复设置可见性
    if (visible !== this.visible) {
      for (let entity of this.entity.values()) {
        drawer.updateObjectVisible(entity.gid, visible)
      }

      this.markVisible(visible)
    }
  }

  // 标记实例的可见性
  markVisible(visible = false) {
    this.visible = visible
  }

  /**
   * 标记是否创建
   */
  markCreated(created = false) {
    this.created = created
  }

  clearEntity() {
    this.entity.clear()
  }

  /**
   * 定位
   * @abstract
   */
  flyTo(config) {
    if (this.data.center) {
      const center = this.data.center
      // center 上还有个 distance 属性，目前这会影响到 flyToPosition 的效果
      const location = {
        x: center.x,
        y: center.y,
        z: center.z,
        pitch: center.pitch,
        yaw: center.yaw,
        roll: center.roll
      }
      this.mapViewer.camera.flyToPositionByOptions({
        position: location,
        duration: config.duration
      })
    }

    // TODO: 要加个 config，允许是否在 flyTo 里做分层
    //  分层的时候，其实也需要做个判断，如果当前楼层就是目标楼层，就不用处理
    //  如果是室内 - 去做分层(建筑的分层和模型的分层)
    //  要考虑的问题在于。这里做了分层。那关闭分层的能力应该是 build 模块处理的,
    //  点位定位的分层，可以配置是否启用
    if (this.data.indoor) {
      const buildId = this.data.build_id
      const floorId = this.data.floor_id

      // 模型的分层。委托给 ModelManage 来处理
      // 暂时就做单个设备的显示和隐藏，因为更简单一下
      const model = this.mapViewer.model
      const adapter = model.getAdapter()
      adapter.setVisible(buildId, floorId)
    }
  }

  /**
   * 模型高亮
   */
  highlight() {
    const entity = this.getModel()
    const mapViewer = this.mapViewer
    this.clearHighlight().then(() => {
      if (entity) {
        // 目前只高亮模型即可
        mapViewer.drawer.setObjectHighlight(entity?.gid)
      }
    })

    return this
  }

  /**
   * 清除高亮统一在 base 里处理即可，目前只有这个方法来实现
   */
  clearHighlight() {
    const mapViewer = this.mapViewer
    const drawer = mapViewer.drawer
    return drawer.clearHighlight()
  }

  /**
   * 根据配置数据.来生成模型, 这里的 entity 需要外界直接传入,
   * 意味着，这里的 create 是更通用的创建模型的方法
   * @abstract
   */
  create() {
    const self = this
    const config = this.config
    const data = this.data || {} // 这里的 data 需要附加 楼层信息，才能满足后续的逻辑
    const drawer = this.mapViewer.drawer
    const event = this.mapViewer.event

    const modelEntity = JSON.parse(JSON.stringify(data))

    const onCreateSuccessHandler = (attr) => {
      self.setModel(attr)

      self.markCreated(true)
      self.markVisible(true)

      config.onSuccess(attr)
    }

    if (config.draw) {
      event.setMousePositionCallback(location => {
        modelEntity.location = location
        drawer.overLayerCreateObject(modelEntity).then(attr => {
          onCreateSuccessHandler(attr, true)
        })
      })
    } else {
      if (config.onMap) {
        drawer.overLayerCreateObject(modelEntity).then(attr => {
          onCreateSuccessHandler(attr, true)
        })
      } else {
        // 这里做一次 setModel 是为了把 entity 缓存起来，方便后续逻辑上统一做批量上图
        self.setModel(modelEntity)
      }
    }
  }

  /**
   * 属性的更新，包括位置，配置项，数据
   * modelInstance.update({
   *   data: cameraProps,
   *   // 位置变化了
   *   changes: newCameraProps.options.location
   * })
   * @desc 更新模型属性和位置
   * @param {Object} updater
   * @param {Object} updater.changes  特指模型位置的更新
   * @param {Object} updater.data 特指与模型关联的数据更新
   */
  update(updater = {}) {
    const { config, changes, data } = updater
    const drawer = this.mapViewer.drawer

    // TODO:有配置项的更新
    if (config) {
      this.setState('config', config)
    }

    // 数据属性更新
    if (data) {
      this.setState('data', data)
    }

    // changes 基本针对地图模型位置的更新, TODO：这里还需要考虑，通用模型的更新，不只有 location 的变化，可能还有其它属性。
    if (changes) {
      let modelEntity = this.getModel()
      let iconEntity = this.getIcon()
      if (iconEntity) {
        iconEntity.data.location = this.getIconLocation(changes)
        drawer.overLayerUpdateObject(iconEntity.data)
      }
      modelEntity.data.location = changes
      drawer.overLayerUpdateObject(modelEntity.data)
    }
  }

  /**
   * @desc 删除模型实例
   */
  destroy() {
    const entity = this.entity
    const drawer = this.mapViewer.drawer
    for (let [key, props] of entity.entries()) {
      drawer.remove.one(props.data)
    }

    this.clearEntity()
  }

  // TODO：更新不同的图标，以表示设备不同的状态1
}

export default ModelBase
