// import { utils } from '@ty/shared'
import * as utils from '../utils'

const { isArray, isFunction, NOOP } = utils
import { warn } from '../error'

class MapCreate {
  constructor(view3d, drawCtl) {
    this._view3d = view3d
    this._drawCtl = drawCtl
    this.config = drawCtl.config
    this.mapViewer = drawCtl._mapView
  }

  _draw(
    config,
    options = {
      isOnce: false,
      onDraw: NOOP,
      onFinish: NOOP,
      drawPoints: true,
      pointsVisible: true
    }
  ) {
    const mapViewer = this.mapViewer
    const { onDraw, onFinish, drawPoints, pointsVisible, isOnce } = options

    const self = this
    let entity = null
    let pointEntities = []

    // 先移除鼠标点击事件
    mapViewer.event.silent()

    const drawCtl = this._drawCtl

    const windowRightClickHandleRef = function windowRightClickHandleRef() {
      drawCtl.overLayerStopEdit()

      // 右键一次后解绑
      window.removeEventListener('contextmenu', windowRightClickHandleRef)

      if (drawPoints) {
        // 删除点位对象
        for (let pointEntity of pointEntities) {
          drawCtl.overLayerRemoveObject(pointEntity)
        }
      }

      // 重新绑定事件
      mapViewer.event.reBind()

      if (isFunction(onFinish)) {
        onFinish(entity)
      }
    }

    window.addEventListener('contextmenu', windowRightClickHandleRef)

    // TOFIX: config中指定的GID无效
    drawCtl.overLayerStartEdit(config, (res) => {
      entity = res
      if (isArray(res.points) && drawPoints) {
        // 这里只添加新增的点
        for (let point of res.points.slice(pointEntities.length)) {
          self
            .point(
              {
                radius: 5,
                color: config.color,
                location: point
              },
              false
            )
            .then((pointEntity) => {
              pointEntities.push(pointEntity)
            })
        }
      }

      if (isFunction(onDraw)) {
        onDraw(res)
      }

      // 对于单个模型的绘制,可以控制是否值绘制一次
      if (isOnce) {
        windowRightClickHandleRef()
      }
    })
  }

  /**
   * 创建文本
   */
  text(config, reBindEvent = false) {
    const drawCtl = this._drawCtl
    const obj = this.config.text(config)
    return drawCtl.overLayerCreateObject(obj, reBindEvent)
  }

  /**
   * 绘制文本
   */
  drawText(options) {
    const config = this.config.text(options)
    const drawOptions = {
      isOnce: true,
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 添加一个模型
   * @description 模型的配置属性有：
   *              attr: {}, 附加的属性，不要附加太多属性，以免批量添加时失败
   *              type: "model",
   *              scale: 1.0 ,
   *              filename: 'box', 材质名称
   *              style: "red",
   *              color: "red",
   *              location: {
   *                x: 0,
   *                y: 0,
   *                z: 0,
   *                pitch: 0, 俯仰角
   *                yaw: 0, 偏航角
   *                roll: 0 翻滚角
   *              }
   * @param config
   * @param config.fileName 模型的材质名称
   * @param config.scale
   * @param config.style
   * @param config.location
   * @param reBindEvent {Boolean} 是否在新建模型后，重新绑定事件
   *
   */
  model(config, reBindEvent = false) {
    const drawCtl = this._drawCtl
    return drawCtl.overLayerCreateObject(this.config.model(config), reBindEvent)
  }

  // 绘制状态下,绘制一个模型
  drawModel(options) {
    const modelConfig = this.config.model(options)
    const modelOptions = {
      isOnce: true,
      onFinish: options.onFinish || NOOP
    }
    this._draw(modelConfig, modelOptions)
  }

  /**
   * 创建图片
   * @param config
   * @param reBindEvent 是否在新建对象后立即重新绑定事件
   */
  image(config, reBindEvent = false) {
    const drawCtl = this._drawCtl
    return drawCtl.overLayerCreateObject(this.config.image(config), reBindEvent)
  }

  /**
   * 绘制图片
   */
  drawImage(options) {
    const config = this.config.image(options)
    const drawOptions = {
      isOnce: true,
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 创建点
   */
  point(config, reBindEvent = false) {
    return this.circle(this.config.point(config), reBindEvent)
  }

  /**
   * 绘制点
   * @param options
   */
  drawPoint(options) {
    const config = this.config.point(options)
    const drawOptions = {
      isOnce: true,
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 创建线
   */
  line(config, reBindEvent = false) {
    if (!config.points || config.points.length < 2) {
      warn(`创建线，必须提供 points 参数，且至少包含两个及以上个点！`)
    }
    return this._drawCtl.overLayerCreateObject(
      this.config.line(config),
      reBindEvent
    )
  }


  /**
   * 绘制线
   */
  drawLine(options) {
    const lineConfig = this.config.line(options)
    const lineOptions = {
      isOnce: false,
      onDraw: options.onDraw || NOOP,
      onFinish: options.onFinish || NOOP,
      drawPoints: options.drawPoints || false,
      pointsVisible: options.pointsVisible || false
    }

    this._draw(lineConfig, lineOptions)
  }

  /**
   * @description 创建面
   * @function
   */
  polygon(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.polygon(config),
      reBindEvent
    )
  }

  /**
   * 绘制面
   * @desc 因为 OverLayerStartEdit 指定 GID 无效，所以需要外部把 gid 指定在 attr 中
   */
  drawPolygon(options = {}) {
    const polygonConfig = this.config.polygon(options)
    const polygonOptions = {
      onDraw: options.onDraw || NOOP,
      onFinish: options.onFinish || NOOP,
      drawPoints: options.drawPoints || true,
      pointsVisible: options.pointsVisible || true
    }

    this._draw(polygonConfig, polygonOptions)
  }

  /**
   * 创建圆
   */
  circle(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.circle(config),
      reBindEvent
    )
  }

  /**
   * 创建POI点
   */
  imageLabel(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.imageLabel(config),
      reBindEvent
    )
  }

  drawImageLabel(options) {
    const config = this.config.imageLabel(options)
    const drawOptions = {
      isOnce: true,
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 创建 3D面
   * @param config
   * @param reBindEvent {Boolean}
   *
   */
  polygon3d(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.polygon3d(config),
      reBindEvent
    )
  }

  /**
   * 绘制 3d 面
   * @param options
   */
  drawPolygon3d(options) {
    const config = this.config.polygon3d(options)
    const drawOptions = {
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 创建三角形
   */
  triangle(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.triangle(config),
      reBindEvent
    )
  }

  /**
   * 绘制三角形
   * @param options
   */
  drawTriangle(options) {
    const config = this.config.triangle(options)
    const drawOptions = {
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 创建矩形
   * @param config {Object}
   * @param reBindEvent{Boolean}
   *
   */
  rect(config, reBindEvent) {
    return this._drawCtl.overLayerCreateObject(
      this.config.rect(config),
      reBindEvent
    )
  }

  /**
   * 绘制矩形
   * @param options
   */
  drawRect(options) {
    const config = this.config.rect(options)
    const drawOptions = {
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 创建规则多边形
   * @param config
   * @param reBindEvent
   *
   */
  regularPolygon(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.regularPolygon(config),
      reBindEvent
    )
  }

  /**
   * 绘制规则多边形
   * @param options
   */
  drawRegularPolygon(options) {
    const config = this.config.regularPolygon(options)
    const drawOptions = {
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 创建形状体
   * @param config
   * @param reBindEvent
   *
   */
  shape(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.shape(config),
      reBindEvent
    )
  }

  /**
   * 绘制形状体
   * @param options
   */
  drawShape(options) {
    const config = this.config.shape(options)
    const drawOptions = {
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 创建 box
   * @param config
   * @param reBindEvent
   *
   */
  box(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.box(config),
      reBindEvent
    )
  }

  drawBox(options) {
    const config = this.config.box(options)
    const drawOptions = {
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 创建金字塔
   * @param config
   * @param reBindEvent
   *
   */
  pyramid(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.pyramid(config),
      reBindEvent
    )
  }

  /**
   * 绘制金字塔
   * @param options
   */
  drawPyramid(options) {
    const config = this.config.pyramid(options)
    const drawOptions = {
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 创建圆锥
   * @param config
   * @param reBindEvent
   *
   */
  cone(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.cone(config),
      reBindEvent
    )
  }

  /**
   * 绘制圆锥
   * @param options
   */
  drawCone(options) {
    const config = this.config.cone(options)
    const drawOptions = {
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 创建圆柱
   * @param config
   * @param reBindEvent
   *
   */
  cylinder(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.cylinder(config),
      reBindEvent
    )
  }

  /**
   * 绘制圆柱
   * @param options
   */
  drawCylinder(options) {
    const config = this.config.cylinder(options)
    const drawOptions = {
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 创建球体
   * @param config
   * @param reBindEvent
   *
   */
  sphere(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(this.config.sphere, reBindEvent)
  }

  /**
   * 绘制球体
   * @param options
   */
  drawSphere(options) {
    const config = this.config.sphere(options)
    const drawOptions = {
      onFinish: options.onFinish || NOOP
    }
    this._draw(config, drawOptions)
  }

  /**
   * 创建骨骼模型
   * @param config
   * @param reBindEvent
   *
   */
  skeleton(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.skeleton,
      reBindEvent
    )
  }

  /**
   * 创建网页广告版
   * todo: 广告版的部分添加详细的使用说明，和遗留问题
   * @param config
   * @param reBindEvent
   *
   */
  billBoard(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.billBoard(config),
      reBindEvent
    )
  }

  /**
   * 创建视频
   * @param config
   * @param reBindEvent
   *
   */
  vlcMedia(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.vlcMedia(config),
      reBindEvent
    )
  }

  /**
   * 创建模型动画
   * @param config
   * @param reBindEvent
   *
   */
  routeModel(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.routeModel(config),
      reBindEvent
    )
  }

  // 普通图层
  layer(config) {
    return this._drawCtl.layerLoadAsync(this.config.layer(config))
  }

  // 动画图层
  layerDynamic(config) {
    return this._drawCtl.layerLoadAsync(this.config.layerDynamic(config))
  }

  // 路径巡游
  route(config) {
    return this._drawCtl.createRoute(this.config.route(config))
  }

  /**
   * 特殊定位点
   * @param config
   * @param reBindEvent
   * @returns {Promise<any>|Promise<unknown>|*}
   */
  niagara(config, reBindEvent = false) {
    return this._drawCtl.overLayerCreateObject(
      this.config.niagara(config),
      reBindEvent
    )
  }
}

export default MapCreate
