class MapDelete {
  constructor(view3d, drawCtl) {
    this._view3d = view3d
    this._drawCtl = drawCtl
  }

  all() {
    this._drawCtl.overLayerRemoveAll()
  }

  one(object) {
    // TODO： 验证删除操作是否影响点击事件
    this._drawCtl.overLayerRemoveObject(object)
  }

  oneById(gid) {
    return this._drawCtl.overLayerRemoveObjectById(gid)
  }
}

export default MapDelete
