import { CAMERA_TYPE_SPEED_DOME } from '../../config'
import Camera from './index'

/**
 * 球机 (Speed Dome Camera)
 */
class CameraSpeedDome extends Camera {
  constructor(mapViewer, ...props) {
    super(mapViewer, ...props)
    this.category = 'CAMERA' // 设备类别的名称.也用做GID的前缀
    this.categoryName = '监控'
    this.type = CAMERA_TYPE_SPEED_DOME
    this.typeName = '球机'
    this.fileName = 'qiuji'

    this.create()
  }
}

export default CameraSpeedDome
