import { CATEGORY_BAYONET } from '../../config'
import Camera from './index'

/**
 * 卡口
 */
class CameraBayonet extends Camera {
  constructor(mapViewer, ...props) {
    super(mapViewer, ...props)
    this.category = 'CAMERA' // 设备类别的名称.也用做GID的前缀
    this.categoryName = '监控'
    this.type = CATEGORY_BAYONET
    this.typeName = '卡口'
    // TODO: 卡口的模型名称可能不是枪机,这里先用枪机
    this.fileName = 'qiangji'

    this.create()
  }
}
export default CameraBayonet
