import ModelBase from '../base'
import { TYPE } from '../config'

class MapImage extends ModelBase {
  constructor(mapViewer, ...props) {
    super(mapViewer, ...props)
    this.type = TYPE.image.typeName
    this.typeName = "image"
    this.$$typeof = "IMAGE"
    this.create()
  }

  create() {
    super.create()
  }

  flyTo(config) {
    super.flyTo(config)
  }

  setVisible(visible) {
    super.setVisible(visible)
  }
}

export default MapImage

