// import { utils } from '@ty/shared'
import * as utils from '../../../../utils'
import { CATEGORY_CAMERA } from '../../config'
import ModelBase from '../../base'

const { EMPTY_OBJ, NOOP } = utils

/**
 * 摄像头基类
 */
class Camera extends ModelBase {
  static mapViewer = null
  static model = '$CAMERA' // 监控的 GID 前缀, 也会用在点击事件里的类型匹配
  static icon = '$ICON_CAMERA' // 监控图标的 GID 前缀
  static area = '$AREA_CAMERA' // 监控可视区域的 GID 前缀

  static _getModelInstance = (entity) => {
    if (!entity) {
      return null
    }

    if (!Camera.mapViewer) {
      return
    }

    const model = Camera.mapViewer.model
    const adapter = model.getAdapter()
    const modelSelector = adapter.getSelector()
    return modelSelector.selectEntityById(entity.attr.id)
  }

  // 模型点击事件实例的处理函数
  static modelClickHandler = (entity) => {
    const modelInstance = Camera._getModelInstance(entity)
    if (modelInstance) {
      modelInstance.flyTo()
    }
  }

  // 模型双击击事件实例的处理函数
  static modelDoubleClickHandler = (entity) => {
    const modelInstance = Camera._getModelInstance(entity)
    if (modelInstance) {
      modelInstance.flyTo()
    }
  }

  // 图标点击事件的处理函数
  static iconClickHandler = (entity) => {
    const modelInstance = Camera._getModelInstance(entity)
    if (modelInstance) {
      modelInstance.flyTo()
    }
  }

  // 图标双击事件的处理函数
  static iconDoubleClickHandler = (entity) => {
    const modelInstance = Camera._getModelInstance(entity)
    if (modelInstance) {
      modelInstance.flyTo()
    }
  }

  // 可视区域点击事件的处理函数
  static areaClickHandler = (entity) => {
    const modelInstance = Camera._getModelInstance(entity)
    if (modelInstance) {
      modelInstance.flyTo()
    }
  }

  // 可视区域双击事件的处理函数
  static areaDoubleClickHandler = (entity) => {
    const modelInstance = Camera._getModelInstance(entity)
    if (modelInstance) {
      modelInstance.flyTo()
    }
  }

  constructor(mapViewer, ...props) {
    super(mapViewer, ...props)
    Camera.mapViewer = mapViewer
    this.category = Camera.model // 设备类别的名称.也用做GID的前缀
    this.categoryName = '监控'
    this.type = CATEGORY_CAMERA
    this.typeName = '监控'
    this.fileName = 'qiangji' // 设备类别的模型名称,对应上图需要的filename
  }

  /**
   * TODO： 设置模型的可见性
   * @param visible
   */
  setModelVisible(visible = true) {}

  /**
   * TODO： 设置模型图标的可见性
   * @param visible
   */
  setIconVisible(visible = true) {}

  // getModel() {
  //   return this.entity.get('model')
  // }
  //
  // getIcon() {
  //   return this.entity.get('icon')
  // }

  getArea() {
    return this.entity.get('area')
  }

  // setModel(props) {
  //   this.setEntity('model', props)
  // }
  //
  // setIcon(props) {
  //   this.setEntity('icon', props)
  // }

  setArea(props) {
    this.setEntity('area', props)
  }

  /**
   * 监控定位
   * @override
   * @return {Camera}
   */
  flyTo(config = { speed: 40, showLocation: false }) {
    super.flyTo(config)
    return this
  }

  // /**
  //  * 监控高亮
  //  * @override
  //  */
  // highlight() {
  //   const entity = this.getModel()
  //   const mapViewer = this.mapViewer
  //   this.clearHighlight().then(() => {
  //     if (entity) {
  //       // 目前只高亮模型即可
  //       mapViewer.drawer.setObjectHighlight(entity?.gid)
  //     }
  //   })
  //
  //   return this
  // }

  /**
   * 模型创建
   * @description 创建模型的实例。根据上图参数 onMap 决定是否实际将模型上图
   * @override
   * @return {ModelBase}
   */
  create() {
    const self = this
    const config = this.config
    const data = this.data || EMPTY_OBJ
    const drawer = this.mapViewer.drawer
    const event = this.mapViewer.event

    // 相机模型
    const modelEntity = drawer.config.model({
      attr: {
        id: data.id,
        gid: this._concatGid(Camera.model, data.id),
      },
      // 指定 GID 还是必须的，是为了后续更新用，
      // 因为批量创建时，每一个 entity 的 GID 必须指定，才能满足后续的操作。
      gid: this._concatGid(Camera.model, data.id),
      fileName: this.fileName,
      scale: 1,
      location: data?.options?.location || null
    })

    // 相机图标
    const imageEntity = drawer.config.image({
      attr: {
        id: data.id,
        gid: this._concatGid(Camera.icon, data.id),
      },
      // screen: false,
      // 指定 GID 还是必须的，是为了后续更新用，
      // 因为批量创建时，每一个 entity 的 GID 必须指定，才能满足后续的操作。
      gid: this._concatGid(Camera.icon, data.id),
      style: this.fileName + '_icon',
      location: self.getIconLocation(modelEntity?.location)
    })

    // const imageEntity = drawer.config.imageLabel({
    //   attr: {
    //     id: data.id
    //   },
    //   // screen: false,
    //   gid: this._concatGid(Camera.icon, data.id),
    //   iconStyle: 'qiangji.png',
    //   text: data?.camera_name,
    //   screen: true,
    //   // style: this.fileName + '_icon',
    //   location: self.getIconLocation(modelEntity?.location)
    // })

    const onCreateSuccessHandler = (entity, shouldUpdateEntity) => {
      if (shouldUpdateEntity) {
        self.setModel(modelEntity)
        self.setIcon(imageEntity)
      }

      self.markVisible(true)
      self.markCreated(true)

      config.onSuccess(entity)
    }

    if (config.draw) {
      // setMousePositionCallback 默认只触发一次
      event.setMousePositionCallback((location) => {
        modelEntity.location = location
        imageEntity.location = self.getIconLocation(location)

        //  绘制成功后,额外添加图标
        drawer.create.image(imageEntity)

        drawer.create.model(modelEntity, true).then(attr => {
          onCreateSuccessHandler(attr, true)
        })

      })
    } else {
      // 这里做一次 setModel 是为了把 entity 缓存起来，方便后续逻辑上统一做批量上图
      self.setModel(modelEntity)
      self.setIcon(imageEntity)

      // 是否上图
      if (config.onMap) {
        drawer.create.model(modelEntity)
        drawer.create.image(imageEntity, true).then(attr => {
          onCreateSuccessHandler(attr, false)
        })
      }
    }

    return this
  }

  /**
   * 属性的更新，包括位置，配置项，数据
   * modelInstance.update({
   *   data: cameraProps,
   *   // 位置变化了
   *   changes: newCameraProps.options.location
   * })
   * @param {Object} updater
   * @param {Object} updater.changes  特指模型位置的更新
   * @param {Object} updater.data 特指与模型关联的数据更新
   * @return {Camera}
   */
  update(updater = {}) {
    super.update(updater)
    return this
  }

  /**
   * 删除当前模型实例，需要把关联的上图的实例都移除
   */
  destroy() {
    super.destroy()
    // TODO：移除可视区域面的逻辑可能略有不同
  }

  // 摄像头可以更新图标
  updateImage() {}

  // 更新相机名称模型
  updateText() {}

  // 更新可视面
  updateVisibleArea() {}

  /**
   * 根据相机上的可视区域属性，创建可视区域面
   */
  createArea() {
    const data = this.data || EMPTY_OBJ
    const drawer = this.mapViewer.drawer
    const self = this
    // 可视区域模型（多边形模型）
    if (data.camera_area) {
      const areaEntityCached = this.getEntity('area')
      if (areaEntityCached) {
        // 已经创建过的实体，让其显示即可
        this.setAreaVisible(true)
      } else {
        // 未创建过的， 去创建，并上图
        const areaEntity = drawer.config.polygon({
          attr: {
            id: data.id
          },
          ...data.camera_area,
          gid: this._concatGid(Camera.area, data.id)
        })
        drawer.create.polygon(areaEntity, true).then((entity) => {
          self.setArea(entity)
        })
      }
    }
  }

  /**
   * 绘制可视区域
   */
  drawArea(callback = NOOP) {
    const mapViewer = this.mapViewer
    const self = this
    const polygonGid = this._concatGid(Camera.area, this.data.id)
    mapViewer.drawer.create.drawPolygon({
      gid: polygonGid,
      attr: {
        // 画面的 GID 指定了无效，所以把 gid 也指定在 attr 中，这样方便在点击事件回到中对事件分类
        id: this.data.id
      },
      onFinish: function (entity) {
        entity && self.setArea(entity)
        callback(entity)
      }
    })
  }

  /**
   * 移除可视区域.删除 entity，移除图上的面
   */
  clearArea() {
    const drawer = this.mapViewer.drawer
    const visibleArea = this.getArea()
    this.deleteEntity('area')
    drawer.remove.one(visibleArea.data)
  }

  /**
   * @override 设置与整个实例关联的所有实体的可见性
   * @param visible
   * @return {Camera}
   */
  setVisible(visible) {
    super.setVisible(visible)

    return this
  }

  /**
   * 设置可视区域的显示和隐藏
   */
  setAreaVisible(visible = true) {
    const drawer = this.mapViewer.drawer
    const visibleAreaEntity = this.getEntity('area')
    if (visibleAreaEntity) {
      drawer.updateObjectVisible(visibleAreaEntity.gid, visible)
    }
  }
}

export default Camera
