import { CAMERA_TYPE_DOME } from '../../config'
import Camera from './index'

/**
 * 半球 (Dome Camera)
 */
class CameraDome extends Camera {
  constructor(mapViewer, ...props) {
    super(mapViewer, ...props)
    this.category = 'CAMERA' // 设备类别的名称.也用做GID的前缀
    this.categoryName = '监控'
    this.type = CAMERA_TYPE_DOME
    this.typeName = '半球'
    this.fileName = 'banqiu'

    this.create()
  }
}

export default CameraDome
