// import { utils } from '@ty/shared'
import * as utils from '../utils'
import * as modelConfig from '../model/category/config'
import { createUUID } from '../utils'

const typeConfig = modelConfig.TYPE

const { objectKeyToLowerCase } = utils

class ModelConfig {
  getTypeName(typeConfig) {
    return typeConfig.typeName
  }

  mergeConfig(config) {
    if (!config.id) {
      config.id = createUUID()
    }

    // 对于不传 GID的情况，可以使用随机的 ID
    if (!config.gid) {
      config.gid = config.id
    }

    return {
      ...config,
      attr: {
        ...config.attr,
        // 显示的再把 gid 赋给 attr ,以避免因为指定 Gid 给地图无效，导致无法匹配上点击事件
        gid: config.gid || config.id
      }
    }
  }

  // 文本
  text(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.text),
      text: '',
      font: '黑体',
      fontcolor: '#ffffff',
      fontsize: 24,
      halign: 'center', // left center right
      valign: 'bottom', // bottom center top
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 模型
  model(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.model),
      filename: 'default',
      scale: 1.0,
      style: 'red',
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 图片
  image(config) {
    // TODO：列出目前的基础图片库
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.image),
      style: 'default', // 材质名称
      scale: 1,
      screen: true,
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 点
  point(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.point),
      radius: 10,
      color: 'red',
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 线
  line(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.line),
      style: 'red',
      linewidth: 10,
      points: [],
      ...objectKeyToLowerCase(config)
    })
  }

  // 面
  polygon(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.polygon),
      points: [],
      // style: config.style || 'green',
      color: '#00ff00',
      // linestyle: 'white',
      linecolor: '#00ff00',
      linewidth: 1,
      linevisible: true,
      ...objectKeyToLowerCase(config)
    })
  }

  // 圆
  circle(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.circle),
      radius: 10,
      color: 'red',
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // POI点
  imageLabel(config) {
    // 使用 POI 点，iconstyle，必须先在平台图像资源里进行导入。
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.imageLabel),
      lighting: true, // 是否支持光照
      iconstyle: 'default.png', // 资源图片里面是否有这个文件
      style: 'white', // 立柱的颜色样式  screen不支持
      scale: 1, // 整个对象的缩放比例
      screen: true,

      // 需要显示文字的，指定一下属性
      // text: '测试文本', // 文本内容
      // fontcolor: '#ffffff', // 文本颜色
      // fontbg: '', // 文本背景颜色    screen不支持背景颜色
      // fontsize: 32.0, // 文本大小        screen不支持字体大小
      // offsetx: 0, // 文本内容偏移位置 x，screen=true单位像素，screen=false 单位为米或厘米，默认 0
      // offsety: 0, // 文本内容偏移位置 ，scyreen=true单位像素，screen=false 单位为米或厘米，默认 0

      // 需要显示立柱的，指定一下两个属性
      // pwidth: 1, // 立柱的宽度  screen不支持
      // pheight: 50.0, // 立柱的高度      screen不支持
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 3D面
  polygon3d(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.polygon3d),
      style: 'green', // 基础样式
      color: '#0000ff', // 基础样式颜色
      linewidth: 5.0, // 边线宽度 单位米
      linestyle: 'green', // 边线样式，优先级高于linecolor
      linecolor: '#0000ff', // 边线颜色，linestyle=null才会起作用
      linevisible: true, // 边线启用
      roofvisible: false, // 顶部面是否启用
      roofheight: 400.0, // 顶部面的高度  单位米
      edgestyle: 'green', // 边缘立面样式，优先级高于edgecolor
      edgecolor: '#0000ff', // 边缘立面颜色，edgestyle=null才会起作用
      edgeheight: 400.0, // 边缘立面高度  单位米
      points: [],
      ...objectKeyToLowerCase(config)
    })
  }

  // 三角形
  triangle(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.triangle),
      color: '#ffffff',
      points: [],
      ...objectKeyToLowerCase(config)
    })
  }

  // 矩形
  rect(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.rect),
      length: 100,
      width: 100,
      color: '#ffffff',
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 规则多边形
  regularPolygon(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.regularPolygon),
      radius: 100, // 半径
      num: 5, // 边数
      color: '#ffffff', // 填充色
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 形状体
  shape(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.shape),
      scale: 1.0, // 缩放比例
      style: 'box', // 模型文件名称
      color: '#ffffff',
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // box
  box(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.box),
      length: 100, // 长
      width: 100, // 宽
      height: 100, // 高
      color: '#ffffff',
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 金字塔
  pyramid(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.pyramid),
      radius: 100, // 半径
      height: 100, // 高度
      color: '#ffffff',
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 圆锥
  cone(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.cone),
      radius: 100, // 半径
      height: 100, // 高
      color: '#ffffff',
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 圆柱
  cylinder(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.cylinder),
      radius: 100, // 半径
      height: 100, // 高
      color: '#ffffff',
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 球体
  sphere(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.sphere),
      scale: 1.0,
      radius: 100, // 半径
      color: '#ffffff',
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 骨骼模型
  skeleton(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.skeleton),
      filename: 'person', // 材质名
      scale: 1.0,
      color: '#ffffff',
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 网页广告版
  billBoard(config) {
    // TODO: check url
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.billBoard),
      // style: 'white',
      url: '',
      width: 100,
      height: 100,
      screen: true, // 是否固定大小， false表示随视角缩放
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 视频
  media(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.media),
      url: '',
      screen: true,
      scale: 1.0,
      width: 100,
      height: 100,
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 模型动画
  dynamicModel(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.dynamicModel),
      scale: 1.0,
      filename: '',
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 路径模型动画
  routeModel(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.routeModel),
      scale: 1,
      speed: 20, // 播放速度， 20m/s
      filename: 'default', // 材质名称
      points: [], // 路径的点位
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 普通图层
  layer(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.layer),
      shapes: [],
      ...objectKeyToLowerCase(config)
    })
  }

  // 动画图层
  layerDynamic(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.layerDynamic),
      speed: 10, // 速度： 经纬度坐标系：m/s，场景坐标系 cm/s.
      shapes: [],
      ...objectKeyToLowerCase(config)
    })
  }

  // 路径巡游
  route(config) {
    // 相机的高度，高度降低后，配合 pitch:35,可实现第一人称视角
    // 90度可表现为上帝视角
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.route),
      visible: true, // 显示隐藏路径
      style: 'sim_arraw', // 路径的样式  sim_arraw, sim_dot, sim_dashed, sim_flash, sim_scan, sim_dot
      showloc: true, // 是否显示起始点图标loc
      width: 100, // 路径的宽度，单位米
      speed: 2000, // 路径的播放，默认2000 厘米/s
      distance: 1000, // 可视距离  > 0.01 单位厘米
      pitch: 35, // 俯仰角， 范围：0——90度 默认35
      iselev: true, // 是否依赖地形高程值 ? todo: 待测试
      geom: [],
      ...objectKeyToLowerCase(config)
    })
  }

  // 动画序列
  videoSequence(id = '') {
    return {
      type: this.getTypeName(typeConfig.videoSequence),
      id
    }
  }

  /**
   * 视频 - RTSP
   */
  video(config) {
    return {
      type: this.getTypeName(typeConfig.video),
      url: '',
      width: 10,
      height: 10,
      position: null,
      ...objectKeyToLowerCase(config)
    }
  }

  // 视频投影
  videoProjection(config) {
    return this.mergeConfig({
      type: this.getTypeName(typeConfig.videoProjection),
      url: '',
      width: 50, //  对象的宽度，单位米 或者 厘米
      height: 45,
      bwidth: 10, // 底边宽度，单位米
      barch: 5, // 底边拱形高度，单位米
      points: [],
      location: null,
      ...objectKeyToLowerCase(config)
    })
  }

  // 区域高亮对象
  region(config) {
    return {
      type: this.getTypeName(typeConfig.region),
      style: 'default',
      geom: null,
      ...objectKeyToLowerCase(config)
    }
  }

  // 热力图
  heatmap(config) {
    return {
      type: this.getTypeName(typeConfig.heatmap),
      pheight: 100, // 对象的高度， 单位： m
      minx: 0, // 热力图范围，
      miny: 0,
      maxx: 0,
      maxy: 0,
      gradient: {}, // 热力图等级
      points: [],
      ...objectKeyToLowerCase(config)
    }
  }

  /**
   * 特殊定位点
   */
  niagara(config) {
    return {
      type: this.getTypeName(typeConfig.heatmap),
      filename: '',
      scale: 1,
      location: null,
      ...objectKeyToLowerCase(config)
    }
  }
}

export default new ModelConfig()
