/**
 * @module sdk-drawer
 * @description 绘图控制器，负责往地图中实际添加对象。
 */

// import { utils } from '@ty/shared'
import * as utils from '../utils'
import MapCreate from './create'
import MapFind from './find'
import MapUpdate from './update'
import MapRemove from './remove'
import MapActor from './actor'

import ModelConfig from './config'
import { functionExist } from '../error'
import { schedule, waitSchedule } from '../scheduler'

const { EMPTY_OBJ, NOOP, objectKeyToLowerCase } = utils

class MapDrawer {
  constructor(view3d, mapView) {
    this._view3d = view3d
    this._event = mapView.event
    this._mapView = mapView
    // this.config = ModelConfig
    this.create = new MapCreate(view3d, this)
    this.find = new MapFind(view3d, this)
    this.remove = new MapRemove(view3d, this)
    this.update = new MapUpdate(view3d, this)
    // this.ACTOR = MapActor
  }

  /**
   * @type class
   * @returns {MapActor}
   * @constructor
   */
  get ACTOR() {
    return MapActor
  }

  /**
   * @type class
   * @returns {ModelConfig}
   */
  get config() {
    return ModelConfig
  }

  // ==== 以下为创建对象 create 相关 ===========
  overLayerSelectObjectEdit(callback = NOOP) {
    const view3d = this._view3d
    const self = this
    if (functionExist(view3d, 'OverLayerSelectObjectEdit')) {
      // 同时注册鼠标右键的事件，右键点击，结束编辑状态
      const windowRightClickHandleRef = function windowRightClickHandleRef() {
        self.overLayerStopEdit()
        window.removeEventListener('contextmenu', windowRightClickHandleRef)
      }

      window.addEventListener('contextmenu', windowRightClickHandleRef)

      view3d.OverLayerSelectObjectEdit((res) => {
        callback && callback(res)
      })
    }
  }

  /**
   * 启用标绘编辑
   * @param config {Object}
   * @param callback {Function}
   * @returns {Promise | Promise<unknown>}
   */
  overLayerStartEdit(config, callback) {
    const view3d = this._view3d
    if (functionExist(view3d, 'OverLayerStartEdit')) {
      return waitSchedule(10, view3d, view3d.OverLayerStartEdit, () => {
        view3d.OverLayerStartEdit(config, callback)
      })
    }
  }

  overLayerStopEdit() {
    const view3d = this._view3d
    if (functionExist(view3d, 'OverLayerStopEdit')) {
      return waitSchedule(10, view3d, view3d.OverLayerStopEdit, () => {
        this._event.reBind()
      })
    }
  }

  overLayerCreateObject(objectConfig = EMPTY_OBJ, reBindEvent = false) {
    const view3d = this._view3d
    if (functionExist(view3d, 'OverLayerCreateObject')) {
      return schedule(
        view3d,
        view3d.OverLayerCreateObject,
        objectConfig,
        (entity) => {
          // 是否在这次创建对象后，立即重新绑定事件。
          if (reBindEvent) {
            this._event.reBind()
          }
        }
      )
    }
  }

  /**
   * 批量创建对象。这是更底层的API，批量创建对象时，建议使用 modelControl 来处理
   * @param objectArray {Array<Object>}
   * @param reBindEvent {Boolean}
   * @returns {Promise<unknown>}
   */
  overLayerCreateObjects(objectArray = [], reBindEvent = false) {
    if (objectArray.length > 200) {
      console.warn(
        'OverLayerCreateObjects 一次创建对象的数量不应超过200个，不然有失败的风险。'
      )
    }

    const view3d = this._view3d
    if (functionExist(view3d, 'OverLayerCreateObjects')) {
      return schedule(
        view3d,
        view3d.OverLayerCreateObjects,
        objectArray,
        () => {
          if (reBindEvent) {
            this._event.reBind()
          }
        }
      )
    }
  }

  // ================================
  // ==== 以下是 查找(find) 对象相关 ====
  /**
   * 根据GID查找对象
   * @param gid {String}
   * @returns {Promise<unknown>}
   */
  findObjectById(gid = '') {
    const view3d = this._view3d
    if (functionExist(view3d, 'FindObjectById')) {
      return schedule(view3d, view3d.FindObjectById, gid)
    }
  }

  /**
   * 根据gid的前缀查找对象
   * @param prefix {String}
   * @param limitNum {Number}
   * @returns {Promise<unknown>}
   */
  findObjectByPrefix(prefix = '', limitNum = 200) {
    const view3d = this._view3d
    if (functionExist(view3d, 'FindObjectByPrefix')) {
      return schedule(view3d, view3d.FindObjectByPrefix, prefix, limitNum)
    }
  }

  // ===================================
  // ==== 以下是 更新(update) 对象相关 ====

  /**
   * 更新对象属性
   */
  overLayerUpdateObject(object) {
    const view3d = this._view3d
    if (functionExist(view3d, 'OverLayerUpdateObject')) {
      view3d.OverLayerUpdateObject(object)
    }
  }

  overLayerUpdateObjectAsync(object) {
    const view3d = this._view3d
    if (functionExist(view3d, 'OverLayerUpdateObject')) {
      return waitSchedule(20, view3d, view3d.OverLayerUpdateObject, object)
    }
  }

  /**
   * 更新对象的样式
   */
  updateObjectStyle(gid, style) {
    const view3d = this._view3d
    if (functionExist(view3d, 'UpdateObjectStyle')) {
      view3d.UpdateObjectStyle(gid, style)
    }
  }

  /**
   * 异步更新对象的样式
   * @param gid {String}
   * @param style {String}
   * @returns {Promise | Promise<unknown>}
   */
  updateObjectStyleAsync(gid, style) {
    const view3d = this._view3d
    if (functionExist(view3d, 'UpdateObjectStyle')) {
      return waitSchedule(20, view3d, view3d.UpdateObjectStyle, gid, style)
    }
  }

  /**
   * 更新对象
   */
  updateObject(object) {
    const view3d = this._view3d
    if (functionExist(view3d, 'UpdateObject')) {
      view3d.UpdateObject(object)
    }
  }

  updateObjectAsync(object) {
    const view3d = this._view3d
    if (functionExist(view3d, 'UpdateObject')) {
      return waitSchedule(20, view3d, view3d.UpdateObject, object)
    }
  }

  /**
   * 更新对象
   */
  updateObjects(objects) {
    const view3d = this._view3d
    if (functionExist(view3d, 'UpdateObjects')) {
      view3d.UpdateObjects(objects)
    }
  }

  /**
   * 重置模型的位置
   */
  resetObjects() {
    const view3d = this._view3d
    if (functionExist(view3d, 'ResetObjects')) {
      view3d.ResetObjects()
    }
  }

  updateObjectsAsync(objects, t = 100) {
    const view3d = this._view3d
    if (functionExist(view3d, 'UpdateObjects')) {
      return waitSchedule(t, view3d, view3d.UpdateObjects, objects)
    }
  }

  /**
   * 清除对象样式
   */
  clearObjectStyle() {
    const view3d = this._view3d
    if (functionExist(view3d, 'ClearObjectStyle')) {
      view3d.ClearObjectStyle()
    }
  }

  /**
   * 获取当前所有 visible 的地图对象
   * @returns {Promise | Promise<unknown>}
   */
  getObjectsVisible() {
    const view3d = this._view3d
    if (functionExist(view3d, 'GetObjectsVisible')) {
      return schedule(view3d, view3d.GetObjectsVisible)
    }
  }

  /**
   * 同步的，批量更新对象的可见性， 通过对象属性的visible属性来决定对象是否可见
   * @param objects {Object} 要更新的对象
   */
  setObjectsVisible(objects) {
    const view3d = this._view3d
    if (functionExist(view3d, 'SetObjectsVisible')) {
      view3d.SetObjectsVisible(objects)
    }
  }

  /**
   * 异步批量更新对象的可见性
   * @param object
   * @param t 指定延迟的时间
   * @returns {Promise<unknown>}
   */
  setObjectsVisibleAsync(object, t = 100) {
    const view3d = this._view3d
    if (functionExist(view3d, 'SetObjectsVisible')) {
      return waitSchedule(t, view3d, view3d.SetObjectsVisible, object)
    }
  }

  /**
   * 更新对象的可见性
   * @param gid
   * @param visible
   */
  updateObjectVisible(gid, visible) {
    const view3d = this._view3d
    if (functionExist(view3d, 'UpdateObjectVisible')) {
      view3d.UpdateObjectVisible(gid, visible)
    }
  }

  /**
   * 异步更新对象的可见性
   * @param gid
   * @param visible
   * @returns {Promise<unknown>}
   */
  updateObjectVisibleAsync(gid, visible) {
    const view3d = this._view3d
    if (functionExist(view3d, 'UpdateObjectVisible')) {
      return waitSchedule(10, view3d, view3d.UpdateObjectVisible, gid, visible)
    }
  }

  // ==========================
  // ==== 以下是 删除 (remove) 对象相关 ====
  /**
   * 删除一个对象， TODO： 移除一个模型，是否还需要重新绑定事件？
   * @param object
   */
  overLayerRemoveObject(object) {
    const view3d = this._view3d
    if (functionExist(view3d, 'OverLayerRemoveObject')) {
      view3d.OverLayerRemoveObject(object)
    }
  }

  /**
   * 通过GID删除对象， TODO： 移除一个模型，是否还需要重新绑定事件？
   * @param gid
   */
  overLayerRemoveObjectById(gid) {
    const view3d = this._view3d
    if (functionExist(view3d, 'OverLayerRemoveObjectById')) {
      return waitSchedule(20, view3d, view3d.OverLayerRemoveObjectById, gid)
    }
  }

  /**
   * 通过GID删除对象， TODO： 移除一个模型，是否还需要重新绑定事件？
   * @param gids {Array<String>}
   */
  overLayerRemoveObjectsById(gids) {
    const view3d = this._view3d
    if (functionExist(view3d, 'OverLayerRemoveObjectsById')) {
      return waitSchedule(20, view3d, view3d.OverLayerRemoveObjectsById, gids)
    }
  }

  /**
   * 删除所有对象， TODO： 移除一个模型，是否还需要重新绑定事件？
   */
  overLayerRemoveAll() {
    const view3d = this._view3d
    if (functionExist(view3d, 'OverLayerRemoveAll')) {
      view3d.OverLayerRemoveAll()
    }
  }

  // =========================
  // ==== 以下是其他相关操作======
  /**
   * 高亮对象
   * @TODO 需要验证，高亮模型是否会因此点击事件，如果影响，需要在这里重新绑定事件
   * @param objId {String | Array}
   * @param waitTime {Number} 因为没有回调，所以指定多少时间来 resolve。
   */
  setObjectHighlight(objId, waitTime = 20) {
    const view3d = this._view3d
    if (functionExist(view3d, 'SetObjectHighlight')) {
      return waitSchedule(20, view3d, view3d.SetObjectHighlight, objId)
      // view3d.SetObjectHighlight(objId)
    }
  }

  /**
   * 通过指定前缀来高亮对象
   * @param prefix {String}
   */
  setObjectsHighlightByPrefix(prefix) {
    if (!prefix) {
      return
    }

    const view3d = this._view3d

    if (functionExist(view3d, 'SetObjectsHighlightByPrefix')) {
      view3d.SetObjectsHighlightByPrefix(prefix)
    }
  }

  /**
   * 清除所有对象的高亮状态
   * @param reBindEvent {Boolean} 是否在清除高亮后，重新绑定鼠标点选事件. 清除高亮总是会影响点击事件，所有默认会重新绑定事件
   * @param waitTime {Number} 因为没有回调，所以指定多少时间来 resolve。
   */
  clearHighlight(reBindEvent = true, waitTime = 20) {
    const view3d = this._view3d
    if (functionExist(view3d, 'ClearHighlight')) {
      return waitSchedule(waitTime, view3d, view3d.ClearHighlight, () => {
        if (reBindEvent) {
          this._event.reBind()
        }
      })

      // view3d.ClearHighlight()
    }
  }

  // 其他  ========================
  // 加载图层 - 同步方法
  layerLoad(config, callback = NOOP) {
    const view3d = this._view3d
    if (functionExist(view3d, 'LayerLoad')) {
      view3d.LayerLoad(config, (res) => {
        callback(res)
      })
    }
  }

  /**
   * 加载图层 - 异步方法
   * @param config
   */
  layerLoadAsync(config) {
    const view3d = this._view3d
    if (functionExist(view3d, 'LayerLoad')) {
      return schedule(view3d, view3d.LayerLoad, config)
    }
  }

  // 设置图层的显示和隐藏
  layerSetVisible(gid = '', visible = true) {
    const view3d = this._view3d
    if (functionExist(view3d, 'LayerSetVisible')) {
      view3d.LayerSetVisible(gid, visible)
    }
  }

  // 设置图层的显示和隐藏 - 异步
  layerSetVisibleAsync(gid = '', visible = true) {
    const view3d = this._view3d
    if (functionExist(view3d, 'LayerSetVisible')) {
      return waitSchedule(20, view3d, view3d.LayerSetVisible, gid, visible)
    }
  }

  /**
   * @todo: 需要测试， 移除图层，是否影响点击事件的绑定，如果影响，需要重新绑定
   * @desc 删除图层
   * @param gid
   */
  layerRemove(gid = '') {
    const view3d = this._view3d
    if (functionExist(view3d, 'LayerRemove')) {
      view3d.LayerRemove(gid)
    }
  }

  // 删除图层 - 异步
  layerRemoveAsync(gid = '') {
    const view3d = this._view3d
    if (functionExist(view3d, 'LayerRemove')) {
      return waitSchedule(20, view3d, view3d.LayerRemove, gid)
    }
  }

  // 更新图层的位置
  layerReLoad(layerObj, callback = NOOP, reBindEvent = false) {
    const view3d = this._view3d
    if (functionExist(view3d, 'LayerReLoad')) {
      view3d.LayerReLoad(layerObj, callback)
    }
  }

  // 更新图层的位置 - 异步
  layerReLoadAsync(layerObj, reBindEvent = false) {
    const view3d = this._view3d
    if (functionExist(view3d, 'LayerReLoad')) {
      return schedule(view3d, view3d.LayerReLoad, layerObj)
    }
  }

  /**
   * 路径巡游
   */
  createRoute(config) {
    const view3d = this._view3d
    if (functionExist(view3d, 'CreateRoute')) {
      return schedule(view3d, view3d.CreateRoute, config)
    }
  }

  playRoute(callback = NOOP) {
    const view3d = this._view3d
    if (functionExist(view3d, 'CreateRoute')) {
      view3d.PlayRoute((res) => {
        callback(res)
      })
    }
  }

  /**
   * 暂停路径巡游
   */
  pauseRoute() {
    const view3d = this._view3d
    if (functionExist(view3d, 'PauseRoute')) {
      view3d.PauseRoute()
    }
  }

  /**
   * 继续播放路径巡游
   */
  resumeRoute(callback) {
    const view3d = this._view3d
    if (functionExist(view3d, 'resumeRoute')) {
      view3d.ResumeRoute(callback)
    }
  }

  /**
   * 停止路径巡游
   */
  stopRoute() {
    const view3d = this._view3d
    if (functionExist(view3d, 'StopRoute')) {
      view3d.StopRoute()
    }
  }

  // 播放动画序列
  playSequence(id) {
    const view3d = this._view3d
    if (functionExist(view3d, 'PlaySequence')) {
      view3d.PlaySequence(id)
    }
  }

  // 暂停动画序列
  pauseSequence(id) {
    const view3d = this._view3d
    if (functionExist(view3d, 'PlaySequence')) {
      view3d.PlaySequence(id)
    }
  }

  // 停止动画序列
  stopSequence(id) {
    const view3d = this._view3d
    if (functionExist(view3d, 'StopSequence')) {
      view3d.StopSequence(id)
    }
  }

  // 判断是否播放中
  isPlayingSequence(id, callback = NOOP) {
    const view3d = this._view3d
    if (functionExist(view3d, 'IsPlayingSequence')) {
      view3d.IsPlayingSequence(id, callback)
    }
  }

  // 判断是否暂停中
  isPausedSequence(id, callback) {
    const view3d = this._view3d
    if (functionExist(view3d, 'IsPausedSequence')) {
      view3d.IsPausedSequence(id, callback)
    }
  }

  // 创建视频
  streamVideoOpen(config) {
    const view3d = this._view3d
    let videoConfig = {
      url: '',
      width: 10, // 单位米  10米
      height: 9, // 单位米  9米
      position: null,
      ...objectKeyToLowerCase(config)
    }
    // TODO: 验证 url,以 rtsp://开头
    if (functionExist(view3d, 'StreamVideoOpen')) {
      view3d.StreamVideoOpen(videoConfig)
    }
  }

  // 关闭视频
  streamVideoClose() {
    const view3d = this._view3d
    if (functionExist(view3d, 'StreamVideoClose')) {
      view3d.StreamVideoClose()
    }
  }

  // 区域高亮
  setHighlightRegion(geom) {
    const view3d = this._view3d
    const pointsData = {
      style: 'default', // 默认样式 目前支持一种样式
      geom: geom
    }
    if (functionExist(view3d, 'SetHighlightRegion')) {
      view3d.SetHighlightRegion(pointsData)
    }
  }

  // 清除区域高亮
  clearHighlightRegion() {
    const view3d = this._view3d
    if (functionExist(view3d, 'ClearHighlightRegion')) {
      view3d.ClearHighlightRegion()
    }
  }
}

export default MapDrawer
